import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { apiBaseUrl } from '../config';

const shuffleProducts = (list) => {
  const items = Array.isArray(list) ? [...list] : [];
  for (let i = items.length - 1; i > 0; i -= 1) {
    const j = Math.floor(Math.random() * (i + 1));
    [items[i], items[j]] = [items[j], items[i]];
  }
  return items;
};

const ShopPage = () => {
  const navigate = useNavigate();
  const [categories, setCategories] = useState([]);
  const [subcategories, setSubcategories] = useState([]);
  const [subcategoriesCache, setSubcategoriesCache] = useState({});
  const [allProducts, setAllProducts] = useState([]);
  const [allProductsLoading, setAllProductsLoading] = useState(false);
  const [allProductsLoaded, setAllProductsLoaded] = useState(false);
  const [allProductsPage, setAllProductsPage] = useState(1);
  const ALL_PRODUCTS_PAGE_SIZE = 50;
  const [selectedCategory, setSelectedCategory] = useState(null);
  const [loading, setLoading] = useState(true);
  const [showProviderModal, setShowProviderModal] = useState(true);

  useEffect(() => {
    setShowProviderModal(true);
  }, []);

  useEffect(() => {
    fetchCategories();
  }, []);

  useEffect(() => {
    if (!selectedCategory) return;
    fetchSubcategories(selectedCategory.id);
  }, [selectedCategory, categories]);

  useEffect(() => {
    if (selectedCategory?.id === 'all' && !allProductsLoaded && !allProductsLoading) {
      fetchAllProducts();
    }
  }, [selectedCategory, allProductsLoaded, allProductsLoading]);

  useEffect(() => {
    if (selectedCategory?.id === 'all') {
      setAllProductsPage(1);
    }
  }, [selectedCategory]);

  useEffect(() => {
    setAllProductsPage(1);
  }, [allProducts.length]);

  useEffect(() => {
    if (selectedCategory?.id === 'all') {
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      });
    }
  }, [allProductsPage, selectedCategory]);

  const fetchCategories = async () => {
    try {
      const response = await fetch(`${apiBaseUrl}/api/categories`);
      if (response.ok) {
        const data = await response.json();
        const formattedCategories = [
          { id: 'all', name: 'All' },
          ...(data.categories || [])
        ];
        setCategories(formattedCategories);
        if (formattedCategories.length > 0) {
          setSelectedCategory(formattedCategories[0]);
        }
      }
    } catch (error) {
      console.error('Error fetching categories:', error);
    } finally {
      setLoading(false);
    }
  };

  const fetchSubcategories = async (categoryId) => {
    if (subcategoriesCache[categoryId]) {
      setSubcategories(subcategoriesCache[categoryId]);
      return;
    }

    if (categoryId === 'all') {
      if (!categories || categories.length <= 1) {
        setSubcategories([]);
        return;
      }

      try {
        const categoryIds = categories
          .filter(cat => cat.id !== 'all')
          .map(cat => cat.id);

        const responses = await Promise.all(
          categoryIds.map(id =>
            fetch(`${apiBaseUrl}/api/subcategories?category_id=${id}`)
              .then(res => (res.ok ? res.json() : { subcategories: [] }))
              .catch(() => ({ subcategories: [] }))
          )
        );

        const aggregated = [];
        const seen = new Map();

        responses.forEach((result, idx) => {
          (result.subcategories || []).forEach(sub => {
            const normalizedSub = {
              ...sub,
              parent_category_id: sub.category_id || sub.parent_category_id || categoryIds[idx]
            };
            if (!seen.has(sub.id)) {
              seen.set(sub.id, normalizedSub);
              aggregated.push(normalizedSub);
            }
          });
        });

        setSubcategories(aggregated);
        setSubcategoriesCache(prev => ({
          ...prev,
          all: aggregated
        }));
      } catch (error) {
        console.error('Error fetching all subcategories:', error);
      }
      return;
    }

    try {
      const response = await fetch(`${apiBaseUrl}/api/subcategories?category_id=${categoryId}`);
      if (response.ok) {
        const data = await response.json();
        const list = (data.subcategories || []).map(sub => ({
          ...sub,
          parent_category_id: sub.category_id || sub.parent_category_id || categoryId
        }));
        setSubcategories(list);
        setSubcategoriesCache(prev => ({
          ...prev,
          [categoryId]: list
        }));
      }
    } catch (error) {
      console.error('Error fetching subcategories:', error);
    }
  };

  const handleCategoryClick = (category) => {
    setSelectedCategory(category);
  };

  const handleSubcategoryClick = (subcategory) => {
    const categoryId = subcategory.category_id || subcategory.parent_category_id || selectedCategory?.id;
    navigate(`/shop/products?category=${categoryId}&subcategory=${subcategory.id}`);
  };

  const handleProductClick = (productId) => {
    navigate(`/shop/product/${productId}`);
  };

  const getTotalAllProductPages = () => {
    if (allProducts.length === 0) return 1;
    return Math.max(1, Math.ceil(allProducts.length / ALL_PRODUCTS_PAGE_SIZE));
  };

  const handleAllProductsPageChange = (nextPage) => {
    const totalPages = getTotalAllProductPages();
    const safePage = Math.min(Math.max(nextPage, 1), totalPages);
    setAllProductsPage(safePage);
  };

  const paginatedAllProducts = selectedCategory?.id === 'all'
    ? allProducts.slice(
        (allProductsPage - 1) * ALL_PRODUCTS_PAGE_SIZE,
        allProductsPage * ALL_PRODUCTS_PAGE_SIZE
      )
    : [];

  const isAdminSoldProduct = (product) => {
    if (!product) return false;
    if (product.is_admin_product) return true;
    if (product.provider_name) {
      return product.provider_name.trim().toLowerCase() === 'admin';
    }
    return false;
  };

  const fetchAllProducts = async () => {
    try {
      setAllProductsLoading(true);
      const response = await fetch(`${apiBaseUrl}/api/products`);
      if (response.ok) {
        const data = await response.json();
        const activeProducts = (data.products || [])
          .filter(product => product.is_active !== false);
        setAllProducts(shuffleProducts(activeProducts));
        setAllProductsLoaded(true);
        setAllProductsPage(1);
      }
    } catch (error) {
      console.error('Error fetching all products:', error);
    } finally {
      setAllProductsLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="shop-page-new page-with-bottom-nav">
        <div className="loading-container">
          <div className="loading-spinner"></div>
          <p>Loading...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="shop-page-new page-with-bottom-nav">
      {/* Header */}
      <div className="shop-header-new">
        <h1>CATEGORIES</h1>
        <button
          className="secondary-btn"
          onClick={() => navigate('/shop-admin/login')}
          style={{
            marginLeft: 'auto',
            marginRight: '12px',
            padding: '4px 10px',
            borderRadius: '14px',
            border: '1px solid #ff6b9d',
            background: '#fff',
            color: '#ff4d91',
            fontWeight: 600,
            fontSize: '11px',
            lineHeight: 1
          }}
        >
          Become a Provider
        </button>
        <div className="header-icons">
          <button className="icon-btn" onClick={() => navigate('/shop/orders')}>
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
              <path d="M9 11l3 3L22 4" />
              <path d="M21 12v4a2 2 0 01-2 2H7l-4 4V4a2 2 0 012-2h9" />
            </svg>
          </button>
          <button className="icon-btn" onClick={() => navigate('/shop/search')}>
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
              <circle cx="11" cy="11" r="8"/>
              <path d="m21 21-4.35-4.35"/>
            </svg>
          </button>
          <button className="icon-btn" onClick={() => navigate('/shop/favorites')}>
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
              <path d="M20.84 4.61a5.5 5.5 0 0 0-7.78 0L12 5.67l-1.06-1.06a5.5 5.5 0 0 0-7.78 7.78l1.06 1.06L12 21.23l7.78-7.78 1.06-1.06a5.5 5.5 0 0 0 0-7.78z"/>
            </svg>
          </button>
        </div>
      </div>

      {/* Category Navigation */}
      <div className="shop-category-nav">
        <div className="category-scroll-track">
          {categories.map(cat => (
            <button
              key={cat.id}
              className={`category-pill ${selectedCategory?.id === cat.id ? 'active' : ''}`}
              onClick={() => handleCategoryClick(cat)}
            >
              <span className="category-pill-icon">
                {cat.name === 'Men' && ''}
                {cat.name === 'Women' && ''}
                {cat.name === 'Kids' && ''}
                {cat.name === 'Electronics' && ''}
                {cat.name === 'Home' && ''}
                {!['Men', 'Women', 'Kids', 'Electronics', 'Home'].includes(cat.name) && ''}
              </span>
              <span className="category-pill-name">{cat.name}</span>
            </button>
          ))}
        </div>
      </div>

      {/* Main Content */}
      <div className="shop-content-new">
        {selectedCategory && (
          <>
            <div className="category-title-section">
              <h2>{selectedCategory.name.toUpperCase()}</h2>
            </div>

            {selectedCategory.id === 'all' ? (
              <div className="all-products-section">
                {allProductsLoading && (
                  <div className="loading-container">
                    <div className="loading-spinner"></div>
                    <p>Loading products...</p>
                  </div>
                )}

                {!allProductsLoading && allProducts.length === 0 && (
                  <div className="no-products-message compact">
                    <div className="no-products-icon"></div>
                    <h3>No products available</h3>
                    <p>Were adding products soon. Please check back later.</p>
                  </div>
                )}

                {!allProductsLoading && allProducts.length > 0 && (
                  <>
                  <div className="products-grid-mobile shop-all-products-grid">
                    {paginatedAllProducts.map(product => (
                      <div
                        key={product.id}
                        className="product-card-mobile"
                        onClick={() => handleProductClick(product.id)}
                        style={{ position: 'relative' }}
                      >
                        {isAdminSoldProduct(product) && (
                          <div
                            style={{
                              position: 'absolute',
                              top: '10px',
                              left: '10px',
                              background: 'linear-gradient(135deg, #f97316 0%, #ef4444 100%)',
                              color: '#fff',
                              padding: '6px 12px',
                              borderRadius: '999px',
                              fontSize: '12px',
                              fontWeight: 700,
                              boxShadow: '0 4px 12px rgba(239,68,68,0.35)',
                              zIndex: 2
                            }}
                          >
                            20% OFF
                          </div>
                        )}
                        <div className="product-image-mobile">
                          {product.images && product.images.length > 0 ? (
                            <img
                              src={product.images[0].startsWith('http') ? product.images[0] : `${apiBaseUrl}${product.images[0]}`}
                              alt={product.title}
                              onError={(e) => {
                                e.target.style.display = 'none';
                                e.target.nextSibling.style.display = 'flex';
                              }}
                            />
                          ) : null}
                          <div
                            className="product-image-placeholder"
                            style={{ display: product.images && product.images.length > 0 ? 'none' : 'flex' }}
                          >
                            <span></span>
                          </div>
                        </div>
                        <div className="product-info-mobile">
                          <h3 className="product-title-mobile">{product.title}</h3>
                          {product.description && (
                            <p className="product-description-mobile">
                              {product.description.length > 80
                                ? `${product.description.substring(0, 80)}...`
                                : product.description}
                            </p>
                          )}
                          {product.discounted_price ? (
                            <div className="product-price-mobile">
                              <span className="price-current">{product.discounted_price}</span>
                              <span className="price-original">{product.original_price}</span>
                            </div>
                          ) : (
                            <div className="product-price-mobile">
                              <span className="price-current">{product.original_price}</span>
                            </div>
                          )}
                        </div>
                      </div>
                    ))}
                  </div>
                  <div className="all-products-pagination">
                    <button
                      className="pagination-btn"
                      onClick={() => handleAllProductsPageChange(allProductsPage - 1)}
                      disabled={allProductsPage <= 1}
                    >
                      Previous
                    </button>
                    <div className="pagination-info">
                      Page {allProductsPage} of {getTotalAllProductPages()}
                      <span>
                        Showing{' '}
                        {allProducts.length === 0
                          ? 0
                          : `${(allProductsPage - 1) * ALL_PRODUCTS_PAGE_SIZE + 1}-${Math.min(
                              allProductsPage * ALL_PRODUCTS_PAGE_SIZE,
                              allProducts.length
                            )}`} of {allProducts.length}
                      </span>
                    </div>
                    <button
                      className="pagination-btn"
                      onClick={() => handleAllProductsPageChange(allProductsPage + 1)}
                      disabled={allProductsPage >= getTotalAllProductPages()}
                    >
                      Next
                    </button>
                  </div>
                  </>
                )}
              </div>
            ) : (
              <>
                {subcategories.length === 0 ? (
                  <div className="no-subcategories">
                    <p>No subcategories available</p>
                  </div>
                ) : (
                  <div className="subcategory-section">
                    <h3 className="section-title">Categories</h3>
                    <div className="subcategory-grid">
                      {subcategories.map(sub => (
                        <div
                          key={sub.id}
                          className="subcategory-card"
                          onClick={() => handleSubcategoryClick(sub)}
                        >
                          <div className="subcategory-image">
                            <div className="placeholder-image">
                              {sub.name.substring(0, 2).toUpperCase()}
                            </div>
                          </div>
                          <div className="subcategory-name">{sub.name}</div>
                        </div>
                      ))}
                    </div>
                  </div>
                )}
              </>
            )}
          </>
        )}
      </div>
      {showProviderModal && (
        <div className="modal-overlay" onClick={() => setShowProviderModal(false)}>
          <div 
            className="modal-content provider-modal" 
            onClick={(e) => e.stopPropagation()}
          >
            <div className="modal-header">
              <h3>Become a Shop Provider</h3>
              <button className="modal-close" onClick={() => setShowProviderModal(false)}></button>
            </div>
            <div className="modal-body">
              <p>
                Reach Unlimited Hub shoppers by listing your products with us. 
                Create a provider account and manage orders, inventory, and promotions from a single dashboard.
              </p>
              <ul>
                <li>Dedicated provider dashboard</li>
                <li>Real-time order tracking</li>
                <li>Promotional tools and analytics</li>
              </ul>
            </div>
            <div className="modal-actions">
              <button className="secondary-btn" onClick={() => setShowProviderModal(false)}>
                Maybe Later
              </button>
              <button
                className="primary-btn"
                onClick={() => {
                  setShowProviderModal(false);
                  navigate('/shop-admin/login');
                }}
              >
                Continue to Provider Login
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default ShopPage;
