import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { apiBaseUrl } from '../config';

const ShopAdminRequestAccess = () => {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    shop_name: '',
    owner_name: '',
    email: '',
    phone: '',
    shop_address: '',
    description: '',
    gst_number: '',
    bank_account_number: '',
    bank_ifsc: '',
    bank_name: '',
    bank_account_holder: ''
  });
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState({ type: '', text: '' });
  const [aadharFile, setAadharFile] = useState(null);
  const [aadharPreview, setAadharPreview] = useState(null);
  const [uploadingAadhar, setUploadingAadhar] = useState(false);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleAadharFileChange = async (e) => {
    const file = e.target.files[0];
    if (!file) return;

    // Validate file type
    const allowedTypes = ['application/pdf', 'image/jpeg', 'image/jpg', 'image/png', 'image/gif', 'image/webp'];
    if (!allowedTypes.includes(file.type)) {
      setMessage({ type: 'error', text: 'Invalid file type. Please upload PDF or image file.' });
      return;
    }

    // Validate file size (max 5MB)
    if (file.size > 5 * 1024 * 1024) {
      setMessage({ type: 'error', text: 'File size should be less than 5MB.' });
      return;
    }

    setAadharFile(file);
    setUploadingAadhar(true);
    setMessage({ type: '', text: '' });

    try {
      const formDataToUpload = new FormData();
      formDataToUpload.append('file', file);

      const response = await fetch(`${apiBaseUrl}/api/shop-admin/upload-document`, {
        method: 'POST',
        body: formDataToUpload
      });

      const data = await response.json();

      if (response.ok) {
        setFormData(prev => ({ ...prev, aadhar_proof_url: data.url }));
        if (file.type.startsWith('image/')) {
          setAadharPreview(data.url);
        }
        setMessage({ type: 'success', text: 'Aadhar proof uploaded successfully' });
      } else {
        setMessage({ type: 'error', text: data.error || 'Failed to upload Aadhar proof' });
        setAadharFile(null);
      }
    } catch (err) {
      setMessage({ type: 'error', text: 'Network error. Please try again.' });
      setAadharFile(null);
    } finally {
      setUploadingAadhar(false);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    const trimmedDescription = formData.description.trim();

    if (!formData.shop_name || !formData.owner_name || !formData.email || !formData.phone || !formData.shop_address || !trimmedDescription) {
      setMessage({ type: 'error', text: 'Please fill in all required fields' });
      return;
    }

    // Validate phone number
    const phoneRegex = /^[+]?[0-9]{10,13}$/;
    if (!phoneRegex.test(formData.phone.replace(/\s/g, ''))) {
      setMessage({ type: 'error', text: 'Please enter a valid phone number' });
      return;
    }

    // Validate email
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(formData.email)) {
      setMessage({ type: 'error', text: 'Please enter a valid email address' });
      return;
    }

    // Validate IFSC if bank account number is provided
    if (formData.bank_account_number && formData.bank_ifsc) {
      const ifscRegex = /^[A-Z]{4}0[A-Z0-9]{6}$/;
      if (!ifscRegex.test(formData.bank_ifsc.toUpperCase())) {
        setMessage({ type: 'error', text: 'Please enter a valid IFSC code (e.g., ABCD0123456)' });
        return;
      }
    }

    try {
      setLoading(true);
      setMessage({ type: '', text: '' });

      // Create FormData for multipart/form-data request
      const submitFormData = new FormData();
      submitFormData.append('shop_name', formData.shop_name);
      submitFormData.append('owner_name', formData.owner_name);
      submitFormData.append('email', formData.email);
      submitFormData.append('phone', formData.phone);
      submitFormData.append('shop_address', formData.shop_address);
      submitFormData.append('description', trimmedDescription);
      submitFormData.append('gst_number', formData.gst_number || '');
      submitFormData.append('bank_account_number', formData.bank_account_number || '');
      submitFormData.append('bank_ifsc', formData.bank_ifsc || '');
      submitFormData.append('bank_name', formData.bank_name || '');
      submitFormData.append('bank_account_holder', formData.bank_account_holder || '');
      
      // If Aadhar file is selected but not uploaded yet, upload it first
      if (aadharFile && !formData.aadhar_proof_url) {
        submitFormData.append('aadhar_proof', aadharFile);
      } else if (formData.aadhar_proof_url) {
        submitFormData.append('aadhar_proof_url', formData.aadhar_proof_url);
      }

      const response = await axios.post(`${apiBaseUrl}/api/shop-admin/request-access`, submitFormData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });

      if (response.status === 201) {
        setMessage({ 
          type: 'success', 
          text: 'Access request submitted successfully! You will be notified once approved.' 
        });
        
        // Redirect to login after 3 seconds
        setTimeout(() => {
          navigate('/shop-admin/login', { 
            state: { 
              message: 'Your access request has been submitted. Please wait for admin approval.',
              phone: formData.phone
            } 
          });
        }, 3000);
      }
    } catch (error) {
      const errorMessage = error.response?.data?.error || 'Failed to submit access request. Please try again.';
      setMessage({ type: 'error', text: errorMessage });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="shop-admin-request-page page-with-bottom-nav">
      <div className="shop-admin-header">
        <button className="back-btn" onClick={() => navigate('/')}>
          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
            <path d="M19 12H5M12 19l-7-7 7-7"/>
          </svg>
        </button>
        <h1>Shop Admin Access Request</h1>
        <div style={{ width: 24 }}></div>
      </div>

      <div className="shop-admin-request-content">
        <div className="request-info-card">
          <h2>Request Shop Admin Access</h2>
          <p>Fill in the form below to request access to the Shop Admin panel. Once approved, you'll be able to upload and manage your products.</p>
        </div>

        <form onSubmit={handleSubmit} className="shop-admin-request-form">
          <div className="form-group">
            <label>Shop Name *</label>
            <input
              type="text"
              name="shop_name"
              value={formData.shop_name}
              onChange={handleChange}
              placeholder="Enter your shop name"
              required
            />
          </div>

          <div className="form-group">
            <label>Owner Name *</label>
            <input
              type="text"
              name="owner_name"
              value={formData.owner_name}
              onChange={handleChange}
              placeholder="Enter owner name"
              required
            />
          </div>

          <div className="form-group">
            <label>Email *</label>
            <input
              type="email"
              name="email"
              value={formData.email}
              onChange={handleChange}
              placeholder="Enter your email"
              required
            />
          </div>

          <div className="form-group">
            <label>Phone Number *</label>
            <input
              type="tel"
              name="phone"
              value={formData.phone}
              onChange={handleChange}
              placeholder="Enter phone number (e.g., +91 98765 43210)"
              required
            />
          </div>

          <div className="form-group">
            <label>Shop Address *</label>
            <textarea
              name="shop_address"
              value={formData.shop_address}
              onChange={handleChange}
              placeholder="Enter complete shop address"
              rows="3"
              required
            />
          </div>

          <div className="form-group">
            <label>Description *</label>
            <textarea
              name="description"
              value={formData.description}
              onChange={handleChange}
              placeholder="Brief description about your shop"
              rows="3"
              required
            />
          </div>

          <div className="form-group">
            <label>GST Number (Optional)</label>
            <input
              type="text"
              name="gst_number"
              value={formData.gst_number}
              onChange={handleChange}
              placeholder="Enter GST number (e.g., 27ABCDE1234F1Z5)"
              maxLength="15"
            />
          </div>

          <div className="form-group">
            <label>Bank Account Details for Settlement</label>
            <input
              type="text"
              name="bank_account_number"
              value={formData.bank_account_number}
              onChange={handleChange}
              placeholder="Account Number"
            />
            <input
              type="text"
              name="bank_ifsc"
              value={formData.bank_ifsc}
              onChange={handleChange}
              placeholder="IFSC Code (e.g., ABCD0123456)"
              style={{ marginTop: '10px' }}
              maxLength="11"
            />
            <input
              type="text"
              name="bank_name"
              value={formData.bank_name}
              onChange={handleChange}
              placeholder="Bank Name"
              style={{ marginTop: '10px' }}
            />
            <input
              type="text"
              name="bank_account_holder"
              value={formData.bank_account_holder}
              onChange={handleChange}
              placeholder="Account Holder Name"
              style={{ marginTop: '10px' }}
            />
          </div>

          <div className="form-group">
            <label>Aadhar Proof *</label>
            <input
              type="file"
              accept=".pdf,.jpg,.jpeg,.png,.gif,.webp"
              onChange={handleAadharFileChange}
              disabled={uploadingAadhar}
              style={{ width: '100%', padding: '10px', marginBottom: '10px', border: '1px solid #ddd', borderRadius: '4px' }}
            />
            {uploadingAadhar && <p style={{ color: '#007bff', fontSize: '14px' }}>Uploading...</p>}
            {aadharPreview && (
              <div style={{ marginTop: '10px' }}>
                <p style={{ fontSize: '14px', color: '#28a745', marginBottom: '5px' }}> Aadhar proof uploaded</p>
                <img src={aadharPreview} alt="Aadhar preview" style={{ maxWidth: '100%', maxHeight: '200px', borderRadius: '4px', border: '1px solid #ddd' }} />
              </div>
            )}
            {formData.aadhar_proof_url && !aadharPreview && (
              <p style={{ fontSize: '14px', color: '#28a745', marginTop: '5px' }}> Aadhar proof uploaded (PDF)</p>
            )}
            <small style={{ fontSize: '12px', color: '#666', display: 'block', marginTop: '5px' }}>
              Upload Aadhar card (PDF or Image, max 5MB)
            </small>
          </div>

          {message.text && (
            <div className={`message ${message.type}`} style={{
              padding: '12px',
              borderRadius: '8px',
              marginBottom: '16px',
              backgroundColor: message.type === 'success' ? '#d1fae5' : '#fee2e2',
              color: message.type === 'success' ? '#065f46' : '#991b1b',
              fontSize: '14px',
              lineHeight: '1.5'
            }}>
              {message.text}
            </div>
          )}

          <button type="submit" className="btn-submit-request" disabled={loading}>
            {loading ? 'Submitting...' : 'Submit Request'}
          </button>
        </form>
      </div>
    </div>
  );
};

export default ShopAdminRequestAccess;

