import React, { createContext, useCallback, useContext, useMemo, useRef, useState } from 'react';

const ToastContext = createContext({
  showToast: () => {}
});

export const ToastProvider = ({ children }) => {
  const [toasts, setToasts] = useState([]);
  const timeoutRefs = useRef({});

  const removeToast = useCallback((id) => {
    setToasts(prev => prev.filter(toast => toast.id !== id));
    if (timeoutRefs.current[id]) {
      clearTimeout(timeoutRefs.current[id]);
      delete timeoutRefs.current[id];
    }
  }, []);

  const showToast = useCallback((message, options = {}) => {
    if (!message) return;
    const id = `${Date.now()}-${Math.random()}`;
    const toast = {
      id,
      message,
      type: options.type || 'info'
    };
    setToasts(prev => [...prev, toast]);

    const duration = typeof options.duration === 'number' ? options.duration : 3500;
    timeoutRefs.current[id] = setTimeout(() => removeToast(id), duration);
  }, [removeToast]);

  const contextValue = useMemo(() => ({ showToast }), [showToast]);

  return (
    <ToastContext.Provider value={contextValue}>
      {children}
      <div className="global-toast-wrapper top-right">
        {toasts.map(toast => (
          <div key={toast.id} className={`global-toast ${toast.type}`}>
            <span>{toast.message}</span>
            <button onClick={() => removeToast(toast.id)}></button>
          </div>
        ))}
      </div>
    </ToastContext.Provider>
  );
};

export const useToast = () => useContext(ToastContext);

