  const normalizeGender = (value) => {
    if (!value) return '';
    const normalized = String(value).trim().toLowerCase();
    if (normalized === 'male') return 'male';
    if (normalized === 'female') return 'female';
    if (normalized === 'other' || normalized === 'others' || normalized === 'non-binary') return 'others';
    return normalized;
  };

  const formatGenderLabel = (value) => {
    const normalized = normalizeGender(value);
    if (normalized === 'male') return 'Male';
    if (normalized === 'female') return 'Female';
    if (normalized === 'others') return 'Others';
    return value || 'Not Provided';
  };

  const normalizeImageSource = (input) => {
    if (!input) return '';

    if (Array.isArray(input)) {
      const firstValid = input.find(item => item);
      return normalizeImageSource(firstValid);
    }

    if (typeof input === 'object') {
      const candidate = input?.url || input?.path || input?.src || input?.image_url;
      if (!candidate) return '';
      return normalizeImageSource(candidate);
    }

    let value = String(input).trim();
    if (!value) return '';

    if (value.startsWith('{') || value.startsWith('[')) {
      try {
        const parsed = JSON.parse(value);
        return normalizeImageSource(parsed);
      } catch (err) {
        console.warn('Failed to parse image source JSON:', err);
      }
    }

    value = value.replace(/\\/g, '/');
    return value;
  };

  const stripApiBaseUrl = (rawUrl) => {
    const url = normalizeImageSource(rawUrl);
    if (!url) return '';
    if (apiBaseUrl && url.startsWith(apiBaseUrl)) {
      return url.slice(apiBaseUrl.length);
    }
    return url;
  };

  const resolveImageUrl = (rawUrl) => {
    const url = normalizeImageSource(rawUrl);
    if (!url) return '';
    if (url.startsWith('http://') || url.startsWith('https://') || url.startsWith('data:')) {
      return url;
    }
    if (url.startsWith('//')) {
      return `${window.location.protocol}${url}`;
    }
    if (!apiBaseUrl) {
      return url;
    }
    if (url.startsWith('/')) {
      return `${apiBaseUrl}${url}`.replace(/([^:]\/)\/+/g, '$1');
    }
    return `${apiBaseUrl}/${url}`.replace(/([^:]\/)\/+/g, '$1');
  };

  const buildImageStateFromUrl = (url) => {
    const normalizedUrl = normalizeImageSource(url);
    const stripped = stripApiBaseUrl(normalizedUrl);
    const finalUrl = stripped || normalizedUrl;
    if (!finalUrl) {
      return [];
    }
    const filename = finalUrl.split('/').pop()?.split('?')[0] || 'image';
    return [{
      url: stripped || finalUrl,
      filename,
      size: 0
    }];
  };

