import React, { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { apiBaseUrl } from '../config';

const AdvertiserRegister = () => {
  const VERIFIED_PHONE_KEY = 'advertiserVerifiedPhone';
  const navigate = useNavigate();
  const location = useLocation();
  const [formData, setFormData] = useState({
    brand_name: '',
    phone_number: '',
    brand_logo: '',
    brand_description: '',
    email: '',
    address: ''
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  useEffect(() => {
    const normalisePhone = (value) => {
      if (!value) return '';
      const digits = value.replace(/[^\d]/g, '').slice(-10);
      if (!digits) return '';
      return `+91${digits}`;
    };

    const storedPhone = (() => {
      try {
        return localStorage.getItem(VERIFIED_PHONE_KEY);
      } catch (storageError) {
        console.warn('Unable to read stored advertiser phone:', storageError);
        return null;
      }
    })();

    const incomingPhone = location.state?.phone || storedPhone;

    if (incomingPhone) {
      const formatted = normalisePhone(incomingPhone);
      setFormData(prev => ({
        ...prev,
        phone_number: formatted
      }));
    }
  }, [location.state, VERIFIED_PHONE_KEY]);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!formData.brand_name || !formData.phone_number) {
      setError('Brand name and phone number are required');
      return;
    }

    const formattedPhone = formData.phone_number.startsWith('+91')
      ? formData.phone_number
      : `+91${formData.phone_number.replace(/[^\d]/g, '').slice(-10)}`;

    setLoading(true);
    setError('');

    try {
      const response = await fetch(`${apiBaseUrl}/api/advertiser/register`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          ...formData,
          phone_number: formattedPhone
        }),
      });

      const data = await response.json();

      if (response.ok) {
        const registeredAdvertiser = {
          ...data.advertiser,
          phone_number: formattedPhone
        };
        try {
          localStorage.removeItem(VERIFIED_PHONE_KEY);
        } catch (storageError) {
          console.warn('Unable to clear stored advertiser phone:', storageError);
        }
        navigate('/advertiser/dashboard', { 
          state: { advertiserId: registeredAdvertiser.id }
        });
      } else {
        setError(data.error || 'Registration failed');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="advertiser-register">
      <div className="advertiser-register-container">
        <div className="advertiser-register-header">
          <h1> Advertiser Registration</h1>
          <p>Create your advertising account and start promoting your brand</p>
        </div>

        <form onSubmit={handleSubmit} className="advertiser-register-form">
          <div className="form-group">
            <label htmlFor="brand_name">Brand Name *</label>
            <input
              type="text"
              id="brand_name"
              name="brand_name"
              value={formData.brand_name}
              onChange={handleInputChange}
              placeholder="Enter your brand name"
              required
            />
          </div>

          <div className="form-group">
            <label htmlFor="phone_number">Phone Number *</label>
            <input
              type="tel"
              id="phone_number"
              name="phone_number"
              value={formData.phone_number}
              onChange={handleInputChange}
              placeholder="Enter your phone number"
              disabled
              readOnly
            />
            <p className="helper-text">Verified during OTP login and locked for security.</p>
          </div>

          <div className="form-group">
            <label htmlFor="brand_logo">Brand Logo URL</label>
            <input
              type="url"
              id="brand_logo"
              name="brand_logo"
              value={formData.brand_logo}
              onChange={handleInputChange}
              placeholder="https://example.com/logo.png"
            />
          </div>

          <div className="form-group">
            <label htmlFor="brand_description">Brand Description</label>
            <textarea
              id="brand_description"
              name="brand_description"
              value={formData.brand_description}
              onChange={handleInputChange}
              placeholder="Tell us about your brand..."
              rows="3"
            />
          </div>

          <div className="form-group">
            <label htmlFor="email">Email</label>
            <input
              type="email"
              id="email"
              name="email"
              value={formData.email}
              onChange={handleInputChange}
              placeholder="Enter your email"
            />
          </div>

          <div className="form-group">
            <label htmlFor="address">Address</label>
            <textarea
              id="address"
              name="address"
              value={formData.address}
              onChange={handleInputChange}
              placeholder="Enter your business address"
              rows="2"
            />
          </div>

          {error && <div className="error-message">{error}</div>}

          <button
            type="submit"
            className="advertiser-register-btn"
            disabled={loading}
          >
            {loading ? 'Creating Account...' : 'Create Account'}
          </button>

          <div className="advertiser-login-footer">
            <p>
              Already have an account?{' '}
              <span className="link" onClick={() => navigate('/advertiser/login')}>
                Login here
              </span>
            </p>
            <p>
              <span className="link" onClick={() => navigate('/login')}>
                 Back to User Login
              </span>
            </p>
          </div>
        </form>
      </div>
    </div>
  );
};

export default AdvertiserRegister;
