import React, { useState, useEffect } from 'react';
import { apiBaseUrl } from '../config';

const SuperAdminPanel = () => {
  const [activeTab, setActiveTab] = useState('admins');
  const [admins, setAdmins] = useState([]);
  const [advertisers, setAdvertisers] = useState([]);
  const [loading, setLoading] = useState(false);
  const [advertisersLoading, setAdvertisersLoading] = useState(false);
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [showPermissionsModal, setShowPermissionsModal] = useState(false);
  const [selectedAdmin, setSelectedAdmin] = useState(null);
  const [permissions, setPermissions] = useState({});
  const [notification, setNotification] = useState(null);
  
  // Form states
  const [newAdmin, setNewAdmin] = useState({
    username: '',
    password: '',
    email: '',
    role: 'admin'
  });
  
  const [permissionForm, setPermissionForm] = useState({});

  const [moduleCategories, setModuleCategories] = useState([]);
  const [allModules, setAllModules] = useState([]); // Flattened list for easy access
  const analyticsInitialFilters = {
    startDate: '',
    endDate: '',
    categoryId: '',
    search: '',
    adminId: ''
  };
  const [productAnalyticsLoading, setProductAnalyticsLoading] = useState(false);
  const [productAnalyticsFilters, setProductAnalyticsFilters] = useState({ ...analyticsInitialFilters });
  const [productAnalyticsData, setProductAnalyticsData] = useState({
    totals: { totalProducts: 0, videoProducts: 0 },
    byAdmin: []
  });
  const [productAnalyticsCategories, setProductAnalyticsCategories] = useState([]);

  useEffect(() => {
    fetchModules();
    if (activeTab === 'admins' || activeTab === 'product-analytics') {
      fetchAdmins();
    }
    if (activeTab === 'advertisers') {
      fetchSuperAdminAdvertisers();
    }
    if (activeTab === 'product-analytics') {
      fetchProductCategories();
      fetchProductAnalytics();
    }
  }, [activeTab]);

  useEffect(() => {
    if (activeTab === 'product-analytics' && admins.length > 0) {
      fetchProductAnalytics();
    }
  }, [admins, activeTab]);

  const fetchModules = async () => {
    try {
      const token = localStorage.getItem('adminToken');
      const response = await fetch(`${apiBaseUrl}/api/super-admin/modules`, {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });
      
      if (response.ok) {
        const data = await response.json();
        setModuleCategories(data.module_categories || []);
        
        // Flatten modules for easy access
        const flatModules = [];
        data.module_categories?.forEach(category => {
          category.modules?.forEach(module => {
            flatModules.push(module);
          });
        });
        setAllModules(flatModules);
      } else {
        console.error('Failed to fetch modules');
      }
    } catch (error) {
      console.error('Error fetching modules:', error);
    }
  };

  const fetchAdmins = async () => {
    setLoading(true);
    try {
      const token = localStorage.getItem('adminToken');
      const response = await fetch(`${apiBaseUrl}/api/super-admin/admin-permissions`, {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });
      
      if (response.ok) {
        const data = await response.json();
        setAdmins(data.admins);
      } else {
        console.error('Failed to fetch admins');
      }
    } catch (error) {
      console.error('Error fetching admins:', error);
    } finally {
      setLoading(false);
    }
  };

  const fetchSuperAdminAdvertisers = async () => {
    setAdvertisersLoading(true);
    try {
      const token = localStorage.getItem('adminToken');
      const headers = token ? { 'Authorization': `Bearer ${token}` } : {};
      const response = await fetch(`${apiBaseUrl}/api/admin/advertisers`, {
        headers
      });

      if (response.ok) {
        const data = await response.json();
        setAdvertisers(data.advertisers || []);
      } else {
        console.error('Failed to fetch advertisers');
      }
    } catch (error) {
      console.error('Error fetching advertisers:', error);
    } finally {
      setAdvertisersLoading(false);
    }
  };

  const fetchProductCategories = async () => {
    try {
      const response = await fetch(`${apiBaseUrl}/api/categories`);
      if (response.ok) {
        const data = await response.json();
        setProductAnalyticsCategories(data.categories || []);
      }
    } catch (error) {
      console.error('Error fetching product categories:', error);
    }
  };

  const fetchProductAnalytics = async (overrideFilters = null) => {
    try {
      setProductAnalyticsLoading(true);
      const token = localStorage.getItem('adminToken');
      const activeFilters = overrideFilters || productAnalyticsFilters;
      const params = new URLSearchParams();

      if (activeFilters.search) {
        params.append('search', activeFilters.search.trim());
      }
      if (activeFilters.categoryId) {
        params.append('category_id', activeFilters.categoryId);
      }
      if (activeFilters.startDate) {
        params.append('start_date', activeFilters.startDate);
      }
      if (activeFilters.endDate) {
        params.append('end_date', activeFilters.endDate);
      }
      if (activeFilters.adminId) {
        params.append('admin_id', activeFilters.adminId);
      }
      const queryString = params.toString() ? `?${params.toString()}` : '';
      const response = await fetch(`${apiBaseUrl}/api/admin/products${queryString}`, {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });

      if (!response.ok) {
        throw new Error(`Failed to fetch product analytics (${response.status})`);
      }

      const data = await response.json();
      const products = data.products || [];
      const summary = data.summary || {};

      const grouped = {};
      products.forEach(product => {
        const key = product.added_by_admin !== null && product.added_by_admin !== undefined
          ? String(product.added_by_admin)
          : 'unknown';

        if (!grouped[key]) {
          grouped[key] = {
            adminId: product.added_by_admin,
            adminUsername: product.added_by_admin_username || 'Unknown',
            adminRole: product.added_by_admin_role || '',
            totalProducts: 0,
            videoProducts: 0
          };
        }

        grouped[key].totalProducts += 1;
        if (product.has_video) {
          grouped[key].videoProducts += 1;
        }
      });

      const processedAdminIds = new Set();
      const analyticsRows = admins.map(admin => {
        const key = String(admin.id);
        processedAdminIds.add(key);
        const metrics = grouped[key] || {
          adminId: admin.id,
          adminUsername: admin.username,
          adminRole: admin.role,
          totalProducts: 0,
          videoProducts: 0
        };
        return {
          adminId: admin.id,
          adminUsername: metrics.adminUsername || admin.username,
          adminRole: metrics.adminRole || admin.role,
          totalProducts: metrics.totalProducts || 0,
          videoProducts: metrics.videoProducts || 0
        };
      });

      Object.entries(grouped).forEach(([key, metrics]) => {
        if (key === 'unknown') {
          return;
        }
        if (!processedAdminIds.has(key)) {
          analyticsRows.push({
            adminId: metrics.adminId,
            adminUsername: metrics.adminUsername || 'Unknown',
            adminRole: metrics.adminRole || '',
            totalProducts: metrics.totalProducts || 0,
            videoProducts: metrics.videoProducts || 0
          });
        }
      });

      if (grouped['unknown']) {
        analyticsRows.push({
          adminId: null,
          adminUsername: grouped['unknown'].adminUsername || 'Unassigned',
          adminRole: grouped['unknown'].adminRole || 'N/A',
          totalProducts: grouped['unknown'].totalProducts || 0,
          videoProducts: grouped['unknown'].videoProducts || 0
        });
      }

      analyticsRows.sort((a, b) => b.totalProducts - a.totalProducts);

      setProductAnalyticsData({
        totals: {
          totalProducts: summary.total_products ?? products.length,
          videoProducts: summary.video_products ?? products.filter(prod => prod.has_video).length
        },
        byAdmin: analyticsRows
      });
    } catch (error) {
      console.error('Error fetching product analytics:', error);
      setProductAnalyticsData({
        totals: { totalProducts: 0, videoProducts: 0 },
        byAdmin: []
      });
    } finally {
      setProductAnalyticsLoading(false);
    }
  };

  const handleProductAnalyticsFilterChange = (field, value) => {
    setProductAnalyticsFilters(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const applyProductAnalyticsFilters = () => {
    fetchProductAnalytics();
  };

  const resetProductAnalyticsFilters = () => {
    const defaults = { ...analyticsInitialFilters };
    setProductAnalyticsFilters(defaults);
    fetchProductAnalytics(defaults);
  };

  const fetchAdminPermissions = async (adminId) => {
    try {
      // Since we already have the admin data with permissions from fetchAdmins,
      // we can extract the permissions directly from the admins array
      const admin = admins.find(a => a.id === adminId);
      if (admin && admin.permissions) {
        setPermissions(admin.permissions);
        return admin.permissions;
      }
      return {};
    } catch (error) {
      console.error('Error fetching permissions:', error);
      return {};
    }
  };

  const handleCreateAdmin = async (e) => {
    e.preventDefault();
    setLoading(true);
    
    try {
      const token = localStorage.getItem('adminToken');
      const response = await fetch(`${apiBaseUrl}/api/admin/super-admin/admins`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify(newAdmin)
      });
      
      if (response.ok) {
        setShowCreateModal(false);
        setNewAdmin({ username: '', password: '', email: '', role: 'admin' });
        setNotification({ type: 'success', message: 'Admin created successfully!' });
        setTimeout(() => setNotification(null), 3000);
        // Secret refresh
        fetchAdmins();
      } else {
        const error = await response.json();
        setNotification({ type: 'error', message: error.error || 'Error creating admin' });
        setTimeout(() => setNotification(null), 3000);
      }
    } catch (error) {
      console.error('Error creating admin:', error);
      setNotification({ type: 'error', message: 'Error creating admin' });
      setTimeout(() => setNotification(null), 3000);
    } finally {
      setLoading(false);
    }
  };

  const handleUpdatePermissions = async (e) => {
    e.preventDefault();
    setLoading(true);
    
    try {
      const token = localStorage.getItem('adminToken');
      
      const response = await fetch(`${apiBaseUrl}/api/super-admin/admin-permissions/${selectedAdmin.id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({ permissions: permissionForm })
      });
      
      if (response.ok) {
        setShowPermissionsModal(false);
        setSelectedAdmin(null);
        setPermissionForm({});
        setNotification({ type: 'success', message: 'Permissions updated successfully!' });
        setTimeout(() => setNotification(null), 3000);
        // Secret refresh
        fetchAdmins();
      } else {
        const error = await response.json();
        setNotification({ type: 'error', message: error.error || 'Error updating permissions' });
        setTimeout(() => setNotification(null), 3000);
      }
    } catch (error) {
      console.error('Error updating permissions:', error);
      setNotification({ type: 'error', message: 'Error updating permissions' });
      setTimeout(() => setNotification(null), 3000);
    } finally {
      setLoading(false);
    }
  };

  const handleDeleteAdmin = async (adminId) => {
    if (!window.confirm('Are you sure you want to delete this admin?')) {
      return;
    }
    
    setLoading(true);
    try {
      const token = localStorage.getItem('adminToken');
      const response = await fetch(`${apiBaseUrl}/api/admin/super-admin/admins/${adminId}`, {
        method: 'DELETE',
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });
      
      if (response.ok) {
        setNotification({ type: 'success', message: 'Admin deleted successfully!' });
        setTimeout(() => setNotification(null), 3000);
        // Secret refresh
        fetchAdmins();
      } else {
        const error = await response.json();
        setNotification({ type: 'error', message: error.error || 'Error deleting admin' });
        setTimeout(() => setNotification(null), 3000);
      }
    } catch (error) {
      console.error('Error deleting admin:', error);
      setNotification({ type: 'error', message: 'Error deleting admin' });
      setTimeout(() => setNotification(null), 3000);
    } finally {
      setLoading(false);
    }
  };

  const openPermissionsModal = async (admin) => {
    setSelectedAdmin(admin);
    const adminPermissions = await fetchAdminPermissions(admin.id);
    
    // Initialize permission form with current permissions
    const formData = {};
    allModules.forEach(module => {
      const existingPerm = adminPermissions[module.key];
      formData[module.key] = {
        can_view: existingPerm?.can_view || false,
        can_create: existingPerm?.can_create || false,
        can_edit: existingPerm?.can_edit || false,
        can_delete: existingPerm?.can_delete || false
      };
    });
    
    setPermissionForm(formData);
    setShowPermissionsModal(true);
  };

  const formatDate = (dateString) => {
    if (!dateString) return 'N/A';
    return new Date(dateString).toLocaleString();
  };

  return (
    <div className="super-admin-panel">
      {/* Notification */}
      {notification && (
        <div className={`notification ${notification.type}`} style={{
          position: 'fixed',
          top: '20px',
          right: '20px',
          padding: '15px 20px',
          borderRadius: '8px',
          backgroundColor: notification.type === 'success' ? '#10b981' : '#ef4444',
          color: 'white',
          boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
          zIndex: 9999,
          animation: 'slideIn 0.3s ease-out'
        }}>
          {notification.message}
        </div>
      )}
      
      <div className="admin-header">
        <h1>Super Admin Panel</h1>
        <div className="admin-tabs">
          <button 
            className={activeTab === 'admins' ? 'active' : ''}
            onClick={() => setActiveTab('admins')}
          >
            Manage Admins
          </button>
          <button
            className={activeTab === 'advertisers' ? 'active' : ''}
            onClick={() => setActiveTab('advertisers')}
          >
            Advertisers
          </button>
          <button
            className={activeTab === 'product-analytics' ? 'active' : ''}
            onClick={() => setActiveTab('product-analytics')}
          >
            Product Analytics
          </button>
        </div>
      </div>

      {activeTab === 'admins' && (
        <div className="admins-section">
          <div className="section-header">
            <h2>Admin Management</h2>
            <button 
              className="btn btn-primary"
              onClick={() => setShowCreateModal(true)}
            >
              Create New Admin
            </button>
          </div>

          {loading ? (
            <div className="loading">Loading admins...</div>
          ) : (
            <div className="admins-table">
              <table>
                <thead>
                  <tr>
                    <th>ID</th>
                    <th>Username</th>
                    <th>Role</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {admins.map(admin => (
                    <tr key={admin.id}>
                      <td>{admin.id}</td>
                      <td>{admin.username}</td>
                      <td>
                        <span className={`role-badge ${admin.role}`}>
                          {admin.role}
                        </span>
                      </td>
                      <td>
                        <div className="action-buttons">
                          <button 
                            className="btn btn-sm btn-secondary"
                            onClick={() => openPermissionsModal(admin)}
                          >
                            Permissions
                          </button>
                          {admin.role !== 'super_admin' && (
                            <button 
                              className="btn btn-sm btn-danger"
                              onClick={() => handleDeleteAdmin(admin.id)}
                            >
                              Delete
                            </button>
                          )}
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </div>
      )}

      {activeTab === 'advertisers' && (
        <div className="advertisers-section" style={{ marginTop: '20px' }}>
          <div className="section-header" style={{ marginBottom: '15px' }}>
            <h2 style={{ marginBottom: '6px' }}>Advertiser Approvals</h2>
            <p style={{ margin: 0, color: '#6b7280' }}>
              Review onboarded advertisers and their approval timestamps.
            </p>
          </div>

          {advertisersLoading ? (
            <div className="loading">Loading advertisers...</div>
          ) : advertisers.length === 0 ? (
            <div className="no-data">No advertisers found.</div>
          ) : (
            <div className="admins-table">
              <table>
                <thead>
                  <tr>
                    <th>Business Name</th>
                    <th>Phone</th>
                    <th>Email</th>
                    <th>Status</th>
                    <th>Approved At</th>
                    <th>Created At</th>
                    <th>Total Ads</th>
                    <th>Total Spent</th>
                  </tr>
                </thead>
                <tbody>
                  {advertisers.map(advertiser => (
                    <tr key={advertiser.id}>
                      <td>{advertiser.business_name || advertiser.brand_name}</td>
                      <td>{advertiser.phone_number || 'N/A'}</td>
                      <td>{advertiser.email || 'N/A'}</td>
                      <td>
                        <span className={`status-badge ${advertiser.status}`}>
                          {advertiser.status === 'active' ? 'Active' : 'Inactive'}
                        </span>
                      </td>
                      <td>{formatDate(advertiser.approved_at)}</td>
                      <td>{formatDate(advertiser.created_at)}</td>
                      <td>{advertiser.total_ads}</td>
                      <td>{advertiser.total_spent}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </div>
      )}

      {activeTab === 'product-analytics' && (
        <div className="product-analytics-section" style={{ marginTop: '20px' }}>
          <div className="section-header" style={{ marginBottom: '15px' }}>
            <h2 style={{ marginBottom: '6px' }}>Product Upload Analytics</h2>
            <p style={{ margin: 0, color: '#6b7280' }}>
              Track how many products each admin has published and how many include video media.
            </p>
          </div>

          <div
            className="analytics-filters-card"
            style={{
              background: '#fff',
              borderRadius: '12px',
              padding: '20px',
              boxShadow: '0 2px 10px rgba(0,0,0,0.06)',
              marginBottom: '20px'
            }}
          >
            <div
              style={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
                flexWrap: 'wrap',
                gap: '15px'
              }}
            >
              <h3 style={{ margin: 0, fontSize: '18px', color: '#1f2937' }}>Filter Analytics</h3>
              <div style={{ display: 'flex', gap: '10px' }}>
                <button
                  type="button"
                  onClick={resetProductAnalyticsFilters}
                  style={{
                    padding: '8px 16px',
                    borderRadius: '6px',
                    border: '1px solid #e5e7eb',
                    background: '#fff',
                    color: '#374151',
                    cursor: 'pointer',
                    fontWeight: 500
                  }}
                >
                  Reset
                </button>
                <button
                  type="button"
                  onClick={applyProductAnalyticsFilters}
                  style={{
                    padding: '8px 18px',
                    borderRadius: '6px',
                    border: 'none',
                    background: 'linear-gradient(135deg, #059669 0%, #34d399 100%)',
                    color: '#fff',
                    cursor: 'pointer',
                    fontWeight: 600,
                    boxShadow: '0 2px 8px rgba(5, 150, 105, 0.25)'
                  }}
                >
                  Apply Filters
                </button>
              </div>
            </div>

            <div
              className="analytics-filter-grid"
              style={{
                display: 'grid',
                gridTemplateColumns: 'repeat(auto-fit, minmax(200px, 1fr))',
                gap: '16px',
                marginTop: '16px'
              }}
            >
              <div>
                <label style={{ display: 'block', fontWeight: 600, fontSize: '13px', marginBottom: '6px', color: '#374151' }}>
                  Search by product name / SKU
                </label>
                <input
                  type="text"
                  value={productAnalyticsFilters.search}
                  onChange={(e) => handleProductAnalyticsFilterChange('search', e.target.value)}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter') {
                      e.preventDefault();
                      applyProductAnalyticsFilters();
                    }
                  }}
                  placeholder="Search products"
                  style={{
                    width: '100%',
                    padding: '10px',
                    border: '1px solid #d1d5db',
                    borderRadius: '6px',
                    fontSize: '14px'
                  }}
                />
              </div>

              <div>
                <label style={{ display: 'block', fontWeight: 600, fontSize: '13px', marginBottom: '6px', color: '#374151' }}>
                  Start Date
                </label>
                <input
                  type="date"
                  value={productAnalyticsFilters.startDate}
                  onChange={(e) => handleProductAnalyticsFilterChange('startDate', e.target.value)}
                  style={{
                    width: '100%',
                    padding: '10px',
                    border: '1px solid #d1d5db',
                    borderRadius: '6px',
                    fontSize: '14px'
                  }}
                />
              </div>

              <div>
                <label style={{ display: 'block', fontWeight: 600, fontSize: '13px', marginBottom: '6px', color: '#374151' }}>
                  End Date
                </label>
                <input
                  type="date"
                  value={productAnalyticsFilters.endDate}
                  onChange={(e) => handleProductAnalyticsFilterChange('endDate', e.target.value)}
                  style={{
                    width: '100%',
                    padding: '10px',
                    border: '1px solid #d1d5db',
                    borderRadius: '6px',
                    fontSize: '14px'
                  }}
                />
              </div>

              <div>
                <label style={{ display: 'block', fontWeight: 600, fontSize: '13px', marginBottom: '6px', color: '#374151' }}>
                  Category
                </label>
                <select
                  value={productAnalyticsFilters.categoryId}
                  onChange={(e) => handleProductAnalyticsFilterChange('categoryId', e.target.value)}
                  style={{
                    width: '100%',
                    padding: '10px',
                    border: '1px solid #d1d5db',
                    borderRadius: '6px',
                    fontSize: '14px',
                    backgroundColor: '#fff'
                  }}
                >
                  <option value="">All Categories</option>
                  {productAnalyticsCategories.map(category => (
                    <option key={category.id} value={category.id}>
                      {category.name}
                    </option>
                  ))}
                </select>
              </div>

              <div>
                <label style={{ display: 'block', fontWeight: 600, fontSize: '13px', marginBottom: '6px', color: '#374151' }}>
                  Admin
                </label>
                <select
                  value={productAnalyticsFilters.adminId}
                  onChange={(e) => handleProductAnalyticsFilterChange('adminId', e.target.value)}
                  style={{
                    width: '100%',
                    padding: '10px',
                    border: '1px solid #d1d5db',
                    borderRadius: '6px',
                    fontSize: '14px',
                    backgroundColor: '#fff'
                  }}
                >
                  <option value="">All Admins</option>
                  {admins.map(admin => (
                    <option key={admin.id} value={admin.id}>
                      {admin.username} ({admin.role})
                    </option>
                  ))}
                </select>
              </div>

            </div>
          </div>

          {productAnalyticsLoading ? (
            <div className="loading">Loading analytics...</div>
          ) : (
            <div className="analytics-results">
              <div
                className="analytics-summary"
                style={{ display: 'flex', gap: '16px', flexWrap: 'wrap', marginBottom: '20px' }}
              >
                <div
                  style={{
                    flex: '1 1 200px',
                    padding: '14px 18px',
                    borderRadius: '12px',
                    background: '#ffffff',
                    color: '#111827',
                    border: '1px solid #e5e7eb',
                    boxShadow: '0 4px 12px rgba(17, 24, 39, 0.08)'
                  }}
                >
                  <div style={{ fontSize: '12px', textTransform: 'uppercase', letterSpacing: '0.05em', color: '#6b7280' }}>
                    Total Products
                  </div>
                  <div style={{ fontSize: '28px', fontWeight: 700, marginTop: '8px', color: '#0f172a' }}>
                    {productAnalyticsData.totals.totalProducts}
                  </div>
                </div>

              </div>

              <div className="analytics-table" style={{ overflowX: 'auto' }}>
                <table style={{ width: '100%', borderCollapse: 'collapse' }}>
                  <thead>
                    <tr style={{ backgroundColor: '#f9fafb' }}>
                      <th style={{ textAlign: 'left', padding: '12px 16px', fontSize: '13px', color: '#111827', textTransform: 'uppercase', letterSpacing: '0.05em' }}>Admin</th>
                      <th style={{ textAlign: 'left', padding: '12px 16px', fontSize: '13px', color: '#111827', textTransform: 'uppercase', letterSpacing: '0.05em' }}>Role</th>
                      <th style={{ textAlign: 'right', padding: '12px 16px', fontSize: '13px', color: '#111827', textTransform: 'uppercase', letterSpacing: '0.05em' }}>Total Products</th>
                    </tr>
                  </thead>
                  <tbody>
                    {productAnalyticsData.byAdmin.length === 0 ? (
                      <tr>
                        <td colSpan="3" style={{ padding: '16px', textAlign: 'center', color: '#6b7280' }}>
                          No products found for the selected filters.
                        </td>
                      </tr>
                    ) : (
                      productAnalyticsData.byAdmin.map(row => {
                        return (
                          <tr key={row.adminId ?? `unknown-${row.adminUsername}`}>
                            <td style={{ padding: '12px 16px', borderTop: '1px solid #e5e7eb', fontWeight: 600, color: '#111827' }}>
                              {row.adminUsername}
                            </td>
                            <td style={{ padding: '12px 16px', borderTop: '1px solid #e5e7eb', color: '#111827' }}>
                              <span className={`role-badge ${row.adminRole || 'unknown'}`}>
                                {row.adminRole || 'N/A'}
                              </span>
                            </td>
                            <td style={{ padding: '12px 16px', borderTop: '1px solid #e5e7eb', textAlign: 'right', fontWeight: 600, color: '#111827' }}>
                              {row.totalProducts}
                            </td>
                          </tr>
                        );
                      })
                    )}
                  </tbody>
                </table>
              </div>
            </div>
          )}
        </div>
      )}

      {/* Create Admin Modal */}
      {showCreateModal && (
        <div className="modal-overlay">
          <div className="modal">
            <div className="modal-header">
              <h3>Create New Admin</h3>
              <button 
                className="close-btn"
                onClick={() => setShowCreateModal(false)}
              >
                
              </button>
            </div>
            <form onSubmit={handleCreateAdmin}>
              <div className="form-group">
                <label>Username:</label>
                <input
                  type="text"
                  value={newAdmin.username}
                  onChange={(e) => setNewAdmin({...newAdmin, username: e.target.value})}
                  required
                />
              </div>
              <div className="form-group">
                <label>Password:</label>
                <input
                  type="password"
                  value={newAdmin.password}
                  onChange={(e) => setNewAdmin({...newAdmin, password: e.target.value})}
                  required
                />
              </div>
              <div className="form-group">
                <label>Email:</label>
                <input
                  type="email"
                  value={newAdmin.email}
                  onChange={(e) => setNewAdmin({...newAdmin, email: e.target.value})}
                />
              </div>
              <div className="form-group">
                <label>Role:</label>
                <select
                  value={newAdmin.role}
                  onChange={(e) => setNewAdmin({...newAdmin, role: e.target.value})}
                >
                  <option value="admin">Admin</option>
                </select>
                <small style={{color: '#6c757d', fontSize: '0.8rem'}}>
                  Only one Super Admin is allowed (admin/admin123)
                </small>
              </div>
              <div className="modal-actions">
                <button type="button" onClick={() => setShowCreateModal(false)}>
                  Cancel
                </button>
                <button type="submit" disabled={loading}>
                  {loading ? 'Creating...' : 'Create Admin'}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}

      {/* Permissions Modal */}
      {showPermissionsModal && selectedAdmin && (
        <div className="modal-overlay">
          <div className="modal permissions-modal">
            <div className="modal-header">
              <h3>Manage Permissions - {selectedAdmin.username}</h3>
              <button 
                className="close-btn"
                onClick={() => setShowPermissionsModal(false)}
              >
                
              </button>
            </div>
            <form onSubmit={handleUpdatePermissions}>
              <div className="permissions-container" style={{ maxHeight: '60vh', overflowY: 'auto' }}>
                {moduleCategories.map((category, categoryIndex) => (
                  <div key={categoryIndex} className="permission-category" style={{
                    marginBottom: '25px',
                    backgroundColor: '#f9fafb',
                    padding: '20px',
                    borderRadius: '12px',
                    border: '1px solid #e5e7eb'
                  }}>
                    <div style={{ marginBottom: '15px' }}>
                      <h3 style={{ 
                        margin: '0 0 5px 0', 
                        color: '#111827',
                        fontSize: '18px',
                        fontWeight: '600'
                      }}>
                        {category.category}
                      </h3>
                      <p style={{ 
                        margin: 0, 
                        fontSize: '13px', 
                        color: '#6b7280'
                      }}>
                        {category.description}
                      </p>
                    </div>
                    
                    <div className="permissions-grid" style={{
                      display: 'grid',
                      gap: '12px'
                    }}>
                      {category.modules
                        ?.filter(module => !['advertisers', 'problems'].includes(module.key))
                        ?.map(module => {
                        const modulePerms = permissionForm[module.key] || {};
                        const hasFullAccess = modulePerms.can_view && modulePerms.can_create && 
                                             modulePerms.can_edit && modulePerms.can_delete;
                        const displayName = module.key === 'ad_sections' ? 'Manage Advertisers' : module.name;
                        
                        return (
                          <div key={module.key} className="permission-module" style={{
                            padding: '15px',
                            border: hasFullAccess ? '2px solid #10b981' : '2px solid #e5e7eb',
                            borderRadius: '8px',
                            backgroundColor: hasFullAccess ? '#f0fdf4' : '#ffffff',
                            transition: 'all 0.2s',
                            boxShadow: '0 1px 2px 0 rgba(0, 0, 0, 0.05)'
                          }}>
                            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start' }}>
                              <div style={{ flex: 1 }}>
                                <h4 style={{ margin: '0 0 5px 0', fontSize: '15px', fontWeight: '500' }}>{displayName}</h4>
                                {module.description && (
                                  <p className="module-description" style={{ 
                                    fontSize: '12px', 
                                    color: '#6b7280', 
                                    margin: '0',
                                    fontWeight: '400'
                                  }}>
                                    {module.description}
                                  </p>
                                )}
                              </div>
                              <label className="permission-checkbox" style={{ 
                                fontWeight: '600', 
                                color: hasFullAccess ? '#10b981' : '#6b7280',
                                cursor: 'pointer',
                                display: 'flex',
                                alignItems: 'center',
                                gap: '8px',
                                fontSize: '14px',
                                marginLeft: '15px',
                                whiteSpace: 'nowrap'
                              }}>
                                <input
                                  type="checkbox"
                                  checked={hasFullAccess}
                                  onChange={(e) => {
                                    const allPerms = e.target.checked;
                                    setPermissionForm({
                                      ...permissionForm,
                                      [module.key]: {
                                        can_view: allPerms,
                                        can_create: allPerms,
                                        can_edit: allPerms,
                                        can_delete: allPerms
                                      }
                                    });
                                  }}
                                  style={{
                                    width: '20px',
                                    height: '20px',
                                    cursor: 'pointer'
                                  }}
                                />
                                {hasFullAccess ? ' Enabled' : 'Enable'}
                              </label>
                            </div>
                          </div>
                        );
                      })}
                    </div>
                  </div>
                ))}
              </div>
              <div className="modal-actions">
                <button type="button" onClick={() => setShowPermissionsModal(false)}>
                  Cancel
                </button>
                <button type="submit" disabled={loading}>
                  {loading ? 'Updating...' : 'Update Permissions'}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default SuperAdminPanel;
