import React, { useEffect, useMemo, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import { apiBaseUrl } from '../config';

const ShopOrdersPage = () => {
  const navigate = useNavigate();
  const { token } = useAuth();
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [expandedOrderId, setExpandedOrderId] = useState(null);

  const expectedDeliveryDate = useMemo(() => {
    const date = new Date();
    date.setDate(date.getDate() + 10);
    return date;
  }, []);

  useEffect(() => {
    const fetchOrders = async () => {
      if (!token) {
        setOrders([]);
        setLoading(false);
        return;
      }

      try {
        setLoading(true);
        setError(null);

        const response = await fetch(`${apiBaseUrl}/api/shop/orders`, {
          headers: {
            'Authorization': `Bearer ${token}`
          }
        });

        if (!response.ok) {
          const payload = await response.json().catch(() => ({}));
          throw new Error(payload.error || 'Failed to load orders');
        }

        const data = await response.json();
        setOrders(data.orders || []);
      } catch (err) {
        console.error('Error loading shop orders:', err);
        setError(err.message || 'Failed to load orders');
      } finally {
        setLoading(false);
      }
    };

    fetchOrders();
  }, [token]);

  const formatDate = (value) => {
    if (!value) return '';
    try {
      const date = new Date(value);
      return isNaN(date.getTime()) ? '' : date.toLocaleDateString();
    } catch (_err) {
      return '';
    }
  };

  const formatCurrency = (amount) => {
    if (amount == null) return '';
    try {
      return `${Number(amount).toLocaleString()}`;
    } catch (_err) {
      return `${amount}`;
    }
  };

  const getStatusLabel = (status) => {
    const labels = {
      'pending': 'Pending Confirmation',
      'confirmed': 'Confirmed',
      'shipped': 'Shipped',
      'delivered': 'Delivered',
      'cancelled': 'Cancelled'
    };
    return labels[status] || status;
  };

  const getStatusClass = (status) => {
    return `order-status-badge status-${status || 'default'}`;
  };

  return (
    <div className="shop-orders-page page-with-bottom-nav">
      <div className="shop-orders-header">
        <button className="back-btn" onClick={() => navigate(-1)}>
          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
            <path d="M19 12H5M12 19l-7-7 7-7"/>
          </svg>
        </button>
        <h1>Your Orders</h1>
        <div style={{ width: 24 }}></div>
      </div>

      {loading ? (
        <div className="loading-container">
          <div className="loading-spinner"></div>
          <p>Loading your orders...</p>
        </div>
      ) : error ? (
        <div className="error-container">
          <p>{error}</p>
          <button className="btn-primary" onClick={() => window.location.reload()}>
            Retry
          </button>
        </div>
      ) : orders.length === 0 ? (
        <div className="empty-state">
          <h2>No Orders Yet</h2>
          <p>Looks like you haven't placed any orders. Start shopping now!</p>
          <button className="btn-primary" onClick={() => navigate('/shop')}>
            Browse Products
          </button>
        </div>
      ) : (
        <div className="orders-list">
          {orders.map(order => {
            const isExpanded = expandedOrderId === order.id;
            return (
              <div key={order.id} className="order-card">
                <div className="order-card-header" onClick={() => setExpandedOrderId(isExpanded ? null : order.id)}>
                  <div>
                    <h2>Order #{order.order_number}</h2>
                    <p>Placed on {formatDate(order.created_at)}</p>
                  </div>
                  <div className="order-card-meta">
                    <span className={getStatusClass(order.status)}>
                      {getStatusLabel(order.status)}
                    </span>
                    <span className="order-card-total">{formatCurrency(order.total_amount)}</span>
                    <button className="expand-btn" aria-label="Toggle order details">
                      <svg
                        width="20"
                        height="20"
                        viewBox="0 0 24 24"
                        fill="none"
                        stroke="currentColor"
                        strokeWidth="2"
                        style={{ transform: isExpanded ? 'rotate(180deg)' : 'rotate(0deg)', transition: 'transform 0.2s ease' }}
                      >
                        <polyline points="6 9 12 15 18 9"/>
                      </svg>
                    </button>
                  </div>
                </div>

                {isExpanded && (
                  <div className="order-card-body">
                    <div className="order-summary">
                      <div>
                        <span className="label">Payment Status</span>
                        <span className={`order-status-badge payment-${order.payment_status}`}>
                          {order.payment_status === 'paid' ? 'Paid' : order.payment_status?.charAt(0).toUpperCase() + order.payment_status?.slice(1)}
                        </span>
                      </div>
                      <div>
                        <span className="label">Expected Delivery</span>
                        <span>{expectedDeliveryDate.toLocaleDateString()}</span>
                      </div>
                    </div>

                    {order.shipping_address && Object.keys(order.shipping_address).length > 0 && (
                      <div className="order-section">
                        <h3>Delivery Address</h3>
                        <div className="address-block">
                          {order.shipping_address.full_name && <p><strong>{order.shipping_address.full_name}</strong></p>}
                          {order.shipping_address.address_line1 && <p>{order.shipping_address.address_line1}</p>}
                          {order.shipping_address.address_line2 && <p>{order.shipping_address.address_line2}</p>}
                          {(order.shipping_address.city || order.shipping_address.state || order.shipping_address.pincode) && (
                            <p>
                              {[order.shipping_address.city, order.shipping_address.state, order.shipping_address.pincode].filter(Boolean).join(', ')}
                            </p>
                          )}
                          {order.shipping_address.phone && <p>Phone: {order.shipping_address.phone}</p>}
                        </div>
                      </div>
                    )}

                    {order.items && order.items.length > 0 && (
                      <div className="order-section">
                        <h3>Items</h3>
                        <div className="order-items">
                          {order.items.map(item => {
                            const productImage = item.product_images && item.product_images.length > 0
                              ? (item.product_images[0].startsWith('http') ? item.product_images[0] : `${apiBaseUrl}${item.product_images[0]}`)
                              : null;
                            return (
                              <div key={item.id} className="order-item">
                                {productImage && (
                                  <img src={productImage} alt={item.product_title} className="order-item-image" />
                                )}
                                <div className="order-item-info">
                                  <h4>{item.product_title}</h4>
                                  <p>Quantity: {item.quantity}</p>
                                  <p>Total: {formatCurrency(item.total_price)}</p>
                                </div>
                              </div>
                            );
                          })}
                        </div>
                      </div>
                    )}
                  </div>
                )}
              </div>
            );
          })}
        </div>
      )}
    </div>
  );
};

export default ShopOrdersPage;

