import React, { useEffect, useMemo, useState } from 'react';
import { apiBaseUrl } from '../config';

const STATUS_TABS = [
  { id: 'all', label: 'All Orders' },
  { id: 'pending', label: 'Pending' },
  { id: 'confirmed', label: 'Confirmed' },
  { id: 'shipped', label: 'Shipped' },
  { id: 'delivered', label: 'Delivered' },
  { id: 'cancelled', label: 'Cancelled' }
];

const STATUS_LABELS = {
  pending: 'Pending Confirmation',
  confirmed: 'Confirmed',
  shipped: 'In Transit',
  delivered: 'Delivered',
  cancelled: 'Cancelled'
};

const STATUS_FLOW = {
  pending: 'confirmed',
  confirmed: 'shipped',
  shipped: 'delivered'
};

const AdminOrdersPanel = () => {
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(false);
  const [activeTab, setActiveTab] = useState('all');
  const [selectedOrder, setSelectedOrder] = useState(null);
  const [showDetailsModal, setShowDetailsModal] = useState(false);
  const [notification, setNotification] = useState(null);

  const expectedDeliveryDate = useMemo(() => {
    const date = new Date();
    date.setDate(date.getDate() + 10);
    return date;
  }, []);

  useEffect(() => {
    fetchOrders(activeTab);
  }, [activeTab]);

  const fetchOrders = async (status) => {
    try {
      setLoading(true);
      const token = localStorage.getItem('adminToken');
      const query = status && status !== 'all' ? `?status=${status}` : '';
      const response = await fetch(`${apiBaseUrl}/api/admin/shop-orders${query}`, {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || 'Failed to fetch orders');
      }

      setOrders(data.orders || []);
    } catch (error) {
      console.error('Error fetching orders:', error);
      setNotification({ type: 'error', message: error.message || 'Failed to fetch orders' });
      setTimeout(() => setNotification(null), 4000);
    } finally {
      setLoading(false);
    }
  };

  const updateOrderStatus = async (orderId, newStatus) => {
    try {
      const token = localStorage.getItem('adminToken');
      const response = await fetch(`${apiBaseUrl}/api/admin/shop-orders/${orderId}/status`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({ status: newStatus })
      });

      const payload = await response.json();

      if (!response.ok) {
        throw new Error(payload.error || 'Failed to update order status');
      }

      setNotification({ type: 'success', message: 'Order status updated successfully' });
      setTimeout(() => setNotification(null), 3000);
      setShowDetailsModal(false);
      setSelectedOrder(null);
      fetchOrders(activeTab);
    } catch (error) {
      console.error('Error updating order status:', error);
      setNotification({ type: 'error', message: error.message || 'Failed to update order status' });
      setTimeout(() => setNotification(null), 4000);
    }
  };

  const getStatusLabel = (status) => STATUS_LABELS[status] || status;

  const getNextStatus = (status) => STATUS_FLOW[status] || null;

  const formatCurrency = (amount) => {
    if (amount == null) return '';
    try {
      return `${Number(amount).toLocaleString()}`;
    } catch (_err) {
      return `${amount}`;
    }
  };

  const formatDate = (value) => {
    if (!value) return '';
    try {
      const d = new Date(value);
      return isNaN(d.getTime()) ? '' : d.toLocaleDateString();
    } catch (_err) {
      return '';
    }
  };

  const renderShippingAddress = (address) => {
    if (!address || Object.keys(address).length === 0) {
      return <div>No shipping address available</div>;
    }

    return (
      <div>
        {address.full_name && <div>{address.full_name}</div>}
        {address.address_line1 && <div>{address.address_line1}</div>}
        {address.address_line2 && <div>{address.address_line2}</div>}
        {(address.city || address.state || address.pincode) && (
          <div>{[address.city, address.state, address.pincode].filter(Boolean).join(', ')}</div>
        )}
        {address.phone && <div>Phone: {address.phone}</div>}
      </div>
    );
  };

  return (
    <div className="admin-orders-panel">
      {notification && (
        <div className={`notification ${notification.type}`} style={{
          position: 'fixed',
          top: '20px',
          right: '20px',
          padding: '15px 20px',
          borderRadius: '8px',
          backgroundColor: notification.type === 'success' ? '#10b981' : '#ef4444',
          color: 'white',
          boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
          zIndex: 9999,
          animation: 'slideIn 0.3s ease-out'
        }}>
          {notification.message}
        </div>
      )}

      <div className="panel-header">
        <h2>User Shop Orders</h2>
      </div>

      <div className="orders-tabs">
        {STATUS_TABS.map((tab) => (
          <button
            key={tab.id}
            className={`tab-btn ${activeTab === tab.id ? 'active' : ''}`}
            onClick={() => setActiveTab(tab.id)}
          >
            {tab.label} ({tab.id === 'all' ? orders.length : orders.filter(o => o.status === tab.id).length})
          </button>
        ))}
      </div>

      {loading ? (
        <div>Loading...</div>
      ) : (
        <div className="orders-list">
          {orders.length === 0 ? (
            <div className="empty-state">No orders found for this filter.</div>
          ) : (
            <table className="orders-table">
              <thead>
                <tr>
                  <th>Order #</th>
                  <th>Customer</th>
                  <th>Status</th>
                  <th>Payment</th>
                  <th>Total</th>
                  <th>Placed On</th>
                  <th>Items</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody>
                {orders.map(order => (
                  <tr key={order.id}>
                    <td>{order.order_number}</td>
                    <td>
                      <div>{order.customer_name || ''}</div>
                      <div className="small-text">{order.customer_phone || ''}</div>
                    </td>
                    <td>
                      <span className={`status-badge status-${order.status}`}>
                        {getStatusLabel(order.status)}
                      </span>
                    </td>
                    <td>
                      <span className={`status-badge payment-${order.payment_status}`}>
                        {order.payment_status?.charAt(0).toUpperCase() + order.payment_status?.slice(1)}
                      </span>
                    </td>
                    <td>{formatCurrency(order.total_amount)}</td>
                    <td>{formatDate(order.created_at)}</td>
                    <td>{order.items ? order.items.length : 0}</td>
                    <td>
                      <button
                        className="btn-sm"
                        onClick={() => {
                          setSelectedOrder(order);
                          setShowDetailsModal(true);
                        }}
                      >
                        View Details
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          )}
        </div>
      )}

      {showDetailsModal && selectedOrder && (
        <div className="modal-overlay" onClick={() => setShowDetailsModal(false)}>
          <div className="modal-content order-details-modal" onClick={(e) => e.stopPropagation()}>
            <h3>Order Details</h3>
            <div className="order-details">
              <div className="detail-row">
                <strong>Order Number:</strong>
                <div>{selectedOrder.order_number}</div>
              </div>
              <div className="detail-row">
                <strong>Status:</strong>
                <div>{getStatusLabel(selectedOrder.status)}</div>
              </div>
              <div className="detail-row">
                <strong>Payment Status:</strong>
                <div>{selectedOrder.payment_status?.charAt(0).toUpperCase() + selectedOrder.payment_status?.slice(1)}</div>
              </div>
              <div className="detail-row">
                <strong>Total Amount:</strong>
                <div>{formatCurrency(selectedOrder.total_amount)}</div>
              </div>
              <div className="detail-row">
                <strong>Placed On:</strong>
                <div>{formatDate(selectedOrder.created_at)}</div>
              </div>
              <div className="detail-row">
                <strong>Expected Delivery:</strong>
                <div>{expectedDeliveryDate.toLocaleDateString()}</div>
              </div>

              <hr />

              <h4>Customer Information</h4>
              <div className="detail-row">
                <strong>Name:</strong>
                <div>{selectedOrder.customer_name || ''}</div>
              </div>
              <div className="detail-row">
                <strong>Phone:</strong>
                <div>{selectedOrder.customer_phone || ''}</div>
              </div>
              <div className="detail-row">
                <strong>Shipping Address:</strong>
                <div>{renderShippingAddress(selectedOrder.shipping_address)}</div>
              </div>

              <hr />

              <h4>Items</h4>
              <div className="detail-row">
                <div>
                  {selectedOrder.items && selectedOrder.items.length > 0 ? (
                    selectedOrder.items.map(item => (
                      <div key={item.id} style={{ marginBottom: '12px' }}>
                        <div><strong>{item.product_title}</strong></div>
                        <div>Quantity: {item.quantity}</div>
                        <div>Total: {formatCurrency(item.total_price)}</div>
                      </div>
                    ))
                  ) : (
                    <div>No items found for this order.</div>
                  )}
                </div>
              </div>

              <hr />

              <div className="status-update-section" style={{ display: 'flex', gap: '12px', flexWrap: 'wrap' }}>
                {getNextStatus(selectedOrder.status) && (
                  <button
                    className="btn-primary"
                    onClick={() => updateOrderStatus(selectedOrder.id, getNextStatus(selectedOrder.status))}
                  >
                    Move to {getStatusLabel(getNextStatus(selectedOrder.status))}
                  </button>
                )}
                {selectedOrder.status !== 'cancelled' && (
                  <button
                    className="btn-secondary"
                    onClick={() => updateOrderStatus(selectedOrder.id, 'cancelled')}
                  >
                    Mark as Cancelled
                  </button>
                )}
              </div>
            </div>
            <div className="modal-actions">
              <button onClick={() => setShowDetailsModal(false)}>Close</button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default AdminOrdersPanel;

