import React, { useMemo, useState } from 'react';
import { apiBaseUrl } from '../config';
import { useToast } from '../context/ToastContext';

const AdminManageAdvertisers = ({
  advertisers = [],
  ads = [],
  loading = false,
  onRefresh,
  onPreviewCampaign,
  onManageCampaign
}) => {
  const { showToast } = useToast();
  const [primaryView, setPrimaryView] = useState('advertisers');
  const [searchTerm, setSearchTerm] = useState('');
  const [statusFilter, setStatusFilter] = useState('all');
  const [sortKey, setSortKey] = useState('recent');
  const [selectedAdvertiserId, setSelectedAdvertiserId] = useState('all');
  const [modalOpen, setModalOpen] = useState(false);
  const [selectedCampaign, setSelectedCampaign] = useState(null);
  const [actionLoading, setActionLoading] = useState(false);
  const [adminNotes, setAdminNotes] = useState('');
  const [profileModalOpen, setProfileModalOpen] = useState(false);
  const [selectedAdvertiser, setSelectedAdvertiser] = useState(null);

  const formatNumber = (value) => {
    if (typeof value === 'number') return value;
    if (!value) return 0;
    const digitsOnly = String(value).replace(/[^\d.-]/g, '');
    const parsed = parseFloat(digitsOnly);
    return Number.isFinite(parsed) ? parsed : 0;
  };

  const extractTextFromHtml = (value) => {
    if (value == null) return '';
    const stringValue = String(value);
    if (typeof window !== 'undefined' && typeof window.DOMParser === 'function') {
      try {
        const parser = new window.DOMParser();
        const doc = parser.parseFromString(stringValue, 'text/html');
        return (doc.body.textContent || doc.body.innerText || '').trim();
      } catch (error) {
        // Fallback to regex stripping when DOMParser fails
      }
    }
    return stringValue.replace(/<[^>]*>/g, ' ').replace(/\s+/g, ' ').trim();
  };

  const normalizeString = (value) => {
    if (value == null) return '';
    const textOnly = extractTextFromHtml(value);
    return textOnly.trim().toLowerCase();
  };

  const canonicalizeStatus = (value) => {
    const normalized = normalizeString(value);
    if (!normalized) return '';

    const exactApproved = ['approved', 'active', 'live', 'enabled', 'verified', 'accepted', 'completed', 'approved_user', 'approved_advertiser'];
    const exactPending = ['pending', 'awaiting', 'under review', 'in_review', 'inreview', 'processing', 'queued', 'submitted', 'pending_review', 'pending_approval'];
    const exactRejected = ['rejected', 'declined', 'denied', 'inactive', 'disabled', 'blocked', 'suspended', 'failed'];

    if (exactApproved.includes(normalized)) return 'approved';
    if (exactPending.includes(normalized)) return 'pending';
    if (exactRejected.includes(normalized)) return 'rejected';

    if (normalized.includes('pending') || normalized.includes('await') || normalized.includes('review')) {
      return 'pending';
    }

    if (normalized.includes('reject') || normalized.includes('declin') || normalized.includes('deny') || normalized.includes('block') || normalized.includes('suspend')) {
      return 'rejected';
    }

    if (
      (normalized.includes('approve') && !normalized.includes('unapprove')) ||
      normalized.includes('active') ||
      normalized.includes('verified') ||
      normalized.includes('accept')
    ) {
      return 'approved';
    }

    return normalized;
  };

  const isVideoSource = (mediaType, mediaUrl) => {
    const normalizedType = normalizeString(mediaType || '');
    if (normalizedType.includes('video')) return true;
    if (!mediaUrl) return false;
    return /\.(mp4|mov|webm|ogg|m4v)(\?.*)?$/i.test(mediaUrl);
  };

  const getAdvertiserStatus = (adv = {}) => {
    const statusCandidates = [
      adv.approval_status,
      adv.moderation_status,
      adv.review_status,
      adv.verification_status,
      adv.kyc_status,
      adv.current_status,
      adv.account_state,
      adv.status,
      adv.account_status,
      adv.stage
    ];

    for (const candidate of statusCandidates) {
      const canonical = canonicalizeStatus(candidate);
      if (canonical) {
        return canonical;
      }
    }

    if (typeof adv.is_approved === 'boolean') {
      return adv.is_approved ? 'approved' : 'rejected';
    }

    if (typeof adv.is_rejected === 'boolean') {
      return adv.is_rejected ? 'rejected' : '';
    }

    if (typeof adv.is_pending === 'boolean') {
      return adv.is_pending ? 'pending' : '';
    }

    if (adv.is_approved != null) {
      const asString = normalizeString(String(adv.is_approved));
      if (['1', 'true', 'yes'].includes(asString)) return 'approved';
      if (['0', 'false', 'no'].includes(asString)) return 'rejected';
    }

    if (adv.is_rejected != null) {
      const asString = normalizeString(String(adv.is_rejected));
      if (['1', 'true', 'yes'].includes(asString)) return 'rejected';
      if (['0', 'false', 'no'].includes(asString)) return '';
    }

    if (adv.is_pending != null) {
      const asString = normalizeString(String(adv.is_pending));
      if (['1', 'true', 'yes'].includes(asString)) return 'pending';
      if (['0', 'false', 'no'].includes(asString)) return '';
    }

    return 'pending';
  };

  const stats = useMemo(() => {
    const totalAdvertisers = advertisers.length;
    const activeAdvertisers = advertisers.filter(
      (adv) => getAdvertiserStatus(adv) === 'approved'
    ).length;
    const pendingAds = ads.filter(
      (ad) => normalizeString(ad.status) === 'pending'
    ).length;
    const approvedAds = ads.filter(
      (ad) => normalizeString(ad.status) === 'approved'
    ).length;
    const totalSpend = advertisers.reduce(
      (sum, adv) => sum + formatNumber(adv.total_spent),
      0
    );

    return {
      totalAdvertisers,
      activeAdvertisers,
      pendingAds,
      approvedAds,
      totalSpend
    };
  }, [advertisers, ads]);

  const advertiserOptions = useMemo(() => {
    return [
      { id: 'approved', label: 'Approved Advertisers' },
      { id: 'pending', label: 'Pending Advertisers' },
      { id: 'rejected', label: 'Rejected Advertisers' }
    ];
  }, []);

  const campaignCategoryOptions = useMemo(() => {
    return [
      { id: 'approved', label: 'Approved Campaigns' },
      { id: 'pending', label: 'Pending Campaigns' },
      { id: 'rejected', label: 'Rejected Campaigns' }
    ];
  }, []);

  const getAdvertiserStatusForAd = (ad) => {
    if (!ad) return '';
    const advertiserId = ad.advertiser_id ?? ad.advertiserId;
    let advertiserRecord = null;

    if (advertiserId != null) {
      advertiserRecord = advertisers.find(
        (adv) => String(adv.id) === String(advertiserId)
      );
    }

    if (!advertiserRecord) {
      const adName = normalizeString(ad.advertiser || ad.advertiser_name || '');
      advertiserRecord = advertisers.find(
        (adv) =>
          normalizeString(adv.brand_name || adv.name || '') === adName
      );
    }

    return getAdvertiserStatus(advertiserRecord || {});
  };

  const filteredAdvertisers = useMemo(() => {
    const normalizedSelectedCategory = normalizeString(selectedAdvertiserId);
    const normalizedStatusFilter = normalizeString(statusFilter);
    return advertisers
      .filter((adv) => {
        if (primaryView !== 'advertisers' || normalizedStatusFilter === 'all') {
          return true;
        }
        return getAdvertiserStatus(adv) === normalizedStatusFilter;
      })
      .filter((adv) => {
        if (!searchTerm.trim()) return true;
        const haystack = [
          adv.brand_name,
          adv.name,
          adv.email,
          adv.phone_number
        ]
          .filter(Boolean)
          .join(' ')
          .toLowerCase();
        return haystack.includes(searchTerm.toLowerCase());
      })
      .filter((adv) => {
        if (selectedAdvertiserId === 'all') {
          return true;
        }
        return getAdvertiserStatus(adv) === normalizedSelectedCategory;
      });
  }, [advertisers, statusFilter, searchTerm, primaryView, selectedAdvertiserId]);

  const filteredAds = useMemo(() => {
    let list = ads;

    if (primaryView === 'ads' && statusFilter !== 'all') {
      list = list.filter(
        (ad) => normalizeString(ad.status) === normalizeString(statusFilter)
      );
    }

    if (primaryView === 'ads' && selectedAdvertiserId !== 'all') {
      const statusFilterValue = normalizeString(selectedAdvertiserId);
      list = list.filter((ad) => canonicalizeStatus(ad.status) === statusFilterValue);
    }

    if (searchTerm.trim()) {
      const term = searchTerm.toLowerCase();
      list = list.filter((ad) => {
        const haystack = [
          ad.name,
          ad.advertiser,
          ad.advertiser_name,
          ad.status
        ]
          .filter(Boolean)
          .join(' ')
          .toLowerCase();
        return haystack.includes(term);
      });
    }

    const sorted = [...list];
    sorted.sort((a, b) => {
      if (sortKey === 'recent') {
        return new Date(b.created_at || b.createdAt || 0) - new Date(a.created_at || a.createdAt || 0);
      }
      if (sortKey === 'performance') {
        const scoreA = formatNumber(a.likes) + formatNumber(a.shares);
        const scoreB = formatNumber(b.likes) + formatNumber(b.shares);
        return scoreB - scoreA;
      }
      if (sortKey === 'earnings') {
        return formatNumber(b.earnings) - formatNumber(a.earnings);
      }
      return 0;
    });

    return sorted;
  }, [ads, advertisers, primaryView, statusFilter, selectedAdvertiserId, searchTerm, sortKey]);

  const statusOptions =
    primaryView === 'advertisers'
      ? [
          { label: 'All', value: 'all' },
          { label: 'Approved', value: 'approved' },
          { label: 'Pending', value: 'pending' },
          { label: 'Rejected', value: 'rejected' }
        ]
      : [
          { label: 'All', value: 'all' },
          { label: 'Approved', value: 'approved' },
          { label: 'Pending', value: 'pending' },
          { label: 'Rejected', value: 'rejected' }
        ];

  const categoryOptions = primaryView === 'ads' ? campaignCategoryOptions : advertiserOptions;
  const categoryAllLabel = primaryView === 'ads' ? 'All campaigns' : 'All categories';
  const categoryLabel = primaryView === 'ads' ? 'Campaign Status' : 'Category';

  const renderBadge = (label, hue) => (
    <span
      style={{
        ...styles.badge,
        borderColor: hue,
        color: hue
      }}
    >
      {label}
    </span>
  );

  const renderAdvertiserCard = (adv) => {
    const derivedStatus = getAdvertiserStatus(adv);
    const badgeColor =
      derivedStatus === 'approved'
        ? '#34d399'
        : derivedStatus === 'pending'
        ? '#fbbf24'
        : derivedStatus === 'rejected'
        ? '#f87171'
        : '#d1d5db';
    const badgeLabel =
      derivedStatus && derivedStatus.length
        ? `${derivedStatus.charAt(0).toUpperCase()}${derivedStatus.slice(1)}`
        : adv.status || 'Unknown';

    return (
      <div key={adv.id || adv.brand_name} style={styles.card}>
        <div style={styles.cardHeader}>
          <div style={styles.brandInfo}>
            <div style={styles.brandLogoWrapper}>
              {adv.brand_logo ? (
                <img
                  src={adv.brand_logo}
                  alt={adv.brand_name || 'Brand logo'}
                  style={styles.brandLogo}
                />
              ) : (
                <div style={styles.logoFallback}>
                  {(adv.brand_name || adv.name || 'AD')[0]}
                </div>
              )}
            </div>
            <div>
              <h4 style={styles.cardTitle}>{adv.brand_name || adv.name || 'Unnamed Advertiser'}</h4>
              <p style={styles.cardSubtitle}>
                {adv.industry || 'General Advertiser'}
              </p>
            </div>
          </div>
          {renderBadge(badgeLabel, badgeColor)}
        </div>

        <div style={styles.cardBody}>
          <div style={styles.infoGrid}>
            <div>
              <p style={styles.infoLabel}>Contact</p>
              <p style={styles.infoValue}>{adv.email || 'Not provided'}</p>
              <p style={styles.infoValue}>{adv.phone_number || 'No phone'}</p>
            </div>
            <div>
              <p style={styles.infoLabel}>Activity</p>
              <p style={styles.infoValue}>
                {adv.total_ads ?? 0} active ads
              </p>
              <p style={styles.infoValue}>
                Spend: {formatNumber(adv.total_spent).toLocaleString('en-IN')}
              </p>
            </div>
          </div>
        </div>

        <div style={styles.cardFooter}>
          <button style={styles.secondaryButton} onClick={() => handleViewProfile(adv)}>View Profile</button>
        </div>
      </div>
    );
  };

  const renderAdCard = (ad) => {
    const status = normalizeString(ad.status);
    const badgeColor =
      status === 'approved'
        ? '#34d399'
        : status === 'pending'
        ? '#fbbf24'
        : status === 'rejected'
        ? '#f87171'
        : '#d1d5db';
    const hasMedia = Boolean(ad.media_url);
    const showVideo = hasMedia && isVideoSource(ad.media_type, ad.media_url);
    const launchExternal = (url) => {
      if (!url || typeof window === 'undefined') return;
      const target = window.open(url, '_blank', 'noopener,noreferrer');
      if (target) target.opener = null;
    };

    const handlePreview = () => {
      if (typeof onPreviewCampaign === 'function') {
        onPreviewCampaign(ad);
        return;
      }
      const url = ad.preview_url || ad.media_url;
      if (url) {
        launchExternal(url);
      } else {
        console.warn('No preview asset available for this campaign', ad);
      }
    };

    const handleManage = () => {
      setSelectedCampaign(ad);
      setAdminNotes('');
      setModalOpen(true);
    };

    return (
      <div key={ad.id || ad.name} style={styles.card}>
        <div style={styles.mediaPreview}>
          {hasMedia ? (
            showVideo ? (
              <video
                style={styles.mediaVideo}
                src={ad.media_url}
                controls
                muted
                playsInline
                poster={ad.media_poster}
              />
            ) : (
              <img
                src={ad.media_url}
                alt={ad.name || 'Advertisement preview'}
                style={styles.mediaImage}
              />
            )
          ) : (
            <div style={styles.mediaFallback}>No media</div>
          )}
          <div style={styles.mediaOverlay}>
            {renderBadge(ad.status || 'Unknown', badgeColor)}
          </div>
        </div>

        <div style={styles.cardBody}>
          <h4 style={styles.cardTitle}>{ad.name || 'Untitled Campaign'}</h4>
          <p style={styles.cardSubtitle}>{ad.advertiser || ad.advertiser_name || 'Unknown Advertiser'}</p>

          <div style={styles.metricsRow}>
            <div>
              <p style={styles.infoLabel}>Likes</p>
              <p style={styles.metricValue}>{formatNumber(ad.likes)}</p>
            </div>
            <div>
              <p style={styles.infoLabel}>Shares</p>
              <p style={styles.metricValue}>{formatNumber(ad.shares)}</p>
            </div>
            <div>
              <p style={styles.infoLabel}>Earnings</p>
              <p style={styles.metricValue}>{formatNumber(ad.earnings).toLocaleString('en-IN')}</p>
            </div>
          </div>
        </div>

        <div style={styles.cardFooter}>
          <span style={styles.timestamp}>
            Created {new Date(ad.created_at || ad.createdAt || Date.now()).toLocaleDateString()}
          </span>
          <div>
            <button style={styles.secondaryButton} onClick={handlePreview}>Preview</button>
            <button style={styles.primaryButton} onClick={handleManage}>Manage</button>
          </div>
        </div>
      </div>
    );
  };

  const handleAccept = async () => {
    if (!selectedCampaign || !selectedCampaign.id) return;
    
    setActionLoading(true);
    try {
      const token = localStorage.getItem('adminToken');
      const response = await fetch(`${apiBaseUrl}/api/admin/approve-ad`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': token ? `Bearer ${token}` : ''
        },
        body: JSON.stringify({
          ad_id: selectedCampaign.id,
          status: 'approved',
          admin_notes: adminNotes.trim() || 'Campaign approved by admin'
        })
      });

      const data = await response.json();

      if (response.ok) {
        showToast('Campaign accepted', { type: 'success' });
        setModalOpen(false);
        setSelectedCampaign(null);
        setAdminNotes('');
        if (typeof onRefresh === 'function') {
          onRefresh();
        }
      } else {
        showToast(data.error || 'Failed to approve campaign', { type: 'error' });
      }
    } catch (error) {
      showToast('Network error. Please try again.', { type: 'error' });
    } finally {
      setActionLoading(false);
    }
  };

  const handleReject = async () => {
    if (!selectedCampaign || !selectedCampaign.id) return;
    
    if (!adminNotes.trim()) {
      showToast('Please provide a reason for rejection', { type: 'error' });
      return;
    }

    setActionLoading(true);
    try {
      const token = localStorage.getItem('adminToken');
      const response = await fetch(`${apiBaseUrl}/api/admin/approve-ad`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': token ? `Bearer ${token}` : ''
        },
        body: JSON.stringify({
          ad_id: selectedCampaign.id,
          status: 'rejected',
          admin_notes: adminNotes.trim()
        })
      });

      const data = await response.json();

      if (response.ok) {
        showToast('Campaign rejected', { type: 'success' });
        setModalOpen(false);
        setSelectedCampaign(null);
        setAdminNotes('');
        if (typeof onRefresh === 'function') {
          onRefresh();
        }
      } else {
        showToast(data.error || 'Failed to reject campaign', { type: 'error' });
      }
    } catch (error) {
      showToast('Network error. Please try again.', { type: 'error' });
    } finally {
      setActionLoading(false);
    }
  };

  const closeModal = () => {
    if (!actionLoading) {
      setModalOpen(false);
      setSelectedCampaign(null);
      setAdminNotes('');
    }
  };

  const handleViewProfile = (advertiser) => {
    setSelectedAdvertiser(advertiser);
    setProfileModalOpen(true);
  };

  const closeProfileModal = () => {
    setProfileModalOpen(false);
    setSelectedAdvertiser(null);
  };

  const renderEmptyState = (title, subtitle) => (
    <div style={styles.emptyState}>
      <h4 style={styles.cardTitle}>{title}</h4>
      <p style={styles.cardSubtitle}>{subtitle}</p>
    </div>
  );

  return (
    <>
      <style>{`
        select option {
          background: #1e293b !important;
          color: #fff !important;
        }
      `}</style>
      <section style={styles.wrapper}>
      <header style={styles.header}>
        <div>
          <p style={styles.subheading}>Advertisement Suite</p>
          <h2 style={styles.heading}>Manage Advertisements</h2>
          <p style={styles.description}>
            Track advertiser relationships, monitor campaign performance, and keep every ad experience consistent across Unlimited Hub.
          </p>
        </div>
        <button
          style={styles.refreshButton}
          onClick={onRefresh}
          disabled={loading}
        >
          {loading ? 'Refreshing' : 'Refresh Data'}
        </button>
      </header>

      <div style={styles.statGrid}>
        <div style={styles.statCard}>
          <p style={styles.statLabel}>Total Advertisers</p>
          <p style={styles.statValue}>{stats.totalAdvertisers}</p>
          <span style={styles.statDescriptor}>Across all verticals</span>
        </div>
        <div style={styles.statCard}>
          <p style={styles.statLabel}>Active Advertisers</p>
          <p style={styles.statValue}>{stats.activeAdvertisers}</p>
          <span style={styles.statDescriptor}>Currently running ads</span>
        </div>
        <div style={styles.statCard}>
          <p style={styles.statLabel}>Pending Campaigns</p>
          <p style={styles.statValue}>{stats.pendingAds}</p>
          <span style={styles.statDescriptor}>Awaiting moderation</span>
        </div>
        <div style={styles.statCard}>
          <p style={styles.statLabel}>Approved Campaigns</p>
          <p style={styles.statValue}>{stats.approvedAds}</p>
          <span style={styles.statDescriptor}>Ready for distribution</span>
        </div>
        <div style={styles.statCard}>
          <p style={styles.statLabel}>Lifetime Spend</p>
          <p style={styles.statValue}>
            {stats.totalSpend.toLocaleString('en-IN')}
          </p>
          <span style={styles.statDescriptor}>Tracked via platform</span>
        </div>
      </div>

      <div style={styles.controls}>
        <div style={styles.searchWrapper}>
          <input
            style={styles.searchInput}
            placeholder="Search by advertiser, campaign, email, or status"
            value={searchTerm}
            onChange={(event) => setSearchTerm(event.target.value)}
          />
        </div>

        <div style={styles.filters}>
          <div style={styles.filterChips}>
            {statusOptions.map((option) => (
              <button
                key={option.value}
                style={{
                  ...styles.filterChip,
                  ...(statusFilter === option.value ? styles.filterChipActive : {})
                }}
                onClick={() => setStatusFilter(option.value)}
              >
                {option.label}
              </button>
            ))}
          </div>

          <div style={styles.viewToggle}>
            <button
              style={{
                ...styles.toggleButton,
                ...(primaryView === 'advertisers' ? styles.toggleActive : {})
              }}
              onClick={() => setPrimaryView('advertisers')}
            >
              Advertisers
            </button>
            <button
              style={{
                ...styles.toggleButton,
                ...(primaryView === 'ads' ? styles.toggleActive : {})
              }}
              onClick={() => setPrimaryView('ads')}
            >
              Campaigns
            </button>
          </div>

          <div style={styles.dropdownGroup}>
            {primaryView === 'ads' && (
              <>
                <label style={styles.dropdownLabel}>Sort by</label>
                <select
                  style={styles.dropdown}
                  value={sortKey}
                  onChange={(event) => setSortKey(event.target.value)}
                >
                  <option value="recent">Most recent</option>
                  <option value="performance">Performance</option>
                  <option value="earnings">Earnings</option>
                </select>
              </>
            )}

            <label style={{ ...styles.dropdownLabel, marginLeft: primaryView === 'ads' ? '16px' : 0 }}>
              {categoryLabel}
            </label>
            <select
              style={styles.dropdown}
              value={selectedAdvertiserId}
              onChange={(event) => setSelectedAdvertiserId(event.target.value)}
            >
              <option value="all">{categoryAllLabel}</option>
              {categoryOptions.map((option) => (
                <option key={option.id} value={option.id}>
                  {option.label}
                </option>
              ))}
            </select>
          </div>
        </div>
      </div>

      {loading ? (
        <div style={styles.loadingState}>Refreshing advertiser intelligence</div>
      ) : (
        <div style={styles.layout}>
          <div style={styles.primaryColumn}>
            {primaryView === 'advertisers' ? (
              filteredAdvertisers.length ? (
                filteredAdvertisers.map(renderAdvertiserCard)
              ) : (
                renderEmptyState(
                  'No advertisers found',
                  'Try expanding your filters or inviting new partners.'
                )
              )
            ) : filteredAds.length ? (
              filteredAds.map(renderAdCard)
            ) : (
              renderEmptyState(
                'No campaigns match your filters',
                'Adjust the filters to review additional campaigns.'
              )
            )}
          </div>
          <aside style={styles.secondaryColumn}>
            <div style={styles.panel}>
              <h4 style={styles.panelTitle}>Platform Guidelines</h4>
              <ul style={styles.panelList}>
                <li>Ensure creatives meet brand-safe standards before approval.</li>
                <li>Pending campaigns expire after 7 days without moderation.</li>
                <li>Share performance snapshots with advertisers weekly.</li>
                <li>Keep all communications inside the platform for auditing.</li>
              </ul>
            </div>

            <div style={styles.panel}>
              <h4 style={styles.panelTitle}>Quick Checks</h4>
              <div style={styles.quickStats}>
                <div>
                  <p style={styles.infoLabel}>New submissions</p>
                  <p style={styles.metricValue}>{stats.pendingAds}</p>
                </div>
                <div>
                  <p style={styles.infoLabel}>Live campaigns</p>
                  <p style={styles.metricValue}>{stats.approvedAds}</p>
                </div>
                <div>
                  <p style={styles.infoLabel}>Engaged advertisers</p>
                  <p style={styles.metricValue}>{stats.activeAdvertisers}</p>
                </div>
              </div>
            </div>
          </aside>
        </div>
      )}

      {modalOpen && selectedCampaign && (
        <div style={styles.modalOverlay} onClick={closeModal}>
          <div style={styles.modalContent} onClick={(e) => e.stopPropagation()}>
            <div style={styles.modalHeader}>
              <h3 style={styles.modalTitle}>Campaign Details</h3>
              <button style={styles.modalCloseButton} onClick={closeModal} disabled={actionLoading}>
                
              </button>
            </div>

            <div style={styles.modalBody}>
              <div style={styles.campaignMediaSection}>
                {selectedCampaign.media_url ? (
                  isVideoSource(selectedCampaign.media_type, selectedCampaign.media_url) ? (
                    <video
                      style={styles.modalMediaVideo}
                      src={selectedCampaign.media_url}
                      controls
                      muted
                      playsInline
                      poster={selectedCampaign.media_poster}
                    />
                  ) : (
                    <img
                      src={selectedCampaign.media_url}
                      alt={selectedCampaign.name || 'Campaign media'}
                      style={styles.modalMediaImage}
                    />
                  )
                ) : (
                  <div style={styles.modalMediaFallback}>No media available</div>
                )}
              </div>

              <div style={styles.campaignInfoSection}>
                <div style={styles.infoRow}>
                  <span style={styles.infoLabel}>Campaign Name:</span>
                  <span style={styles.infoValue}>{selectedCampaign.name || 'Untitled Campaign'}</span>
                </div>
                <div style={styles.infoRow}>
                  <span style={styles.infoLabel}>Advertiser:</span>
                  <span style={styles.infoValue}>{selectedCampaign.advertiser || selectedCampaign.advertiser_name || 'Unknown'}</span>
                </div>
                <div style={styles.infoRow}>
                  <span style={styles.infoLabel}>Status:</span>
                  <span style={styles.infoValue}>{selectedCampaign.status || 'Unknown'}</span>
                </div>
                <div style={styles.infoRow}>
                  <span style={styles.infoLabel}>Likes:</span>
                  <span style={styles.infoValue}>{formatNumber(selectedCampaign.likes)}</span>
                </div>
                <div style={styles.infoRow}>
                  <span style={styles.infoLabel}>Shares:</span>
                  <span style={styles.infoValue}>{formatNumber(selectedCampaign.shares)}</span>
                </div>
                <div style={styles.infoRow}>
                  <span style={styles.infoLabel}>Earnings:</span>
                  <span style={styles.infoValue}>{formatNumber(selectedCampaign.earnings).toLocaleString('en-IN')}</span>
                </div>
                <div style={styles.infoRow}>
                  <span style={styles.infoLabel}>Created:</span>
                  <span style={styles.infoValue}>
                    {new Date(selectedCampaign.created_at || selectedCampaign.createdAt || Date.now()).toLocaleString()}
                  </span>
                </div>
                {selectedCampaign.description && (
                  <div style={styles.infoRow}>
                    <span style={styles.infoLabel}>Description:</span>
                    <span style={styles.infoValue}>{selectedCampaign.description}</span>
                  </div>
                )}
                {selectedCampaign.target_audience && (
                  <div style={styles.infoRow}>
                    <span style={styles.infoLabel}>Target Audience:</span>
                    <span style={styles.infoValue}>{selectedCampaign.target_audience}</span>
                  </div>
                )}
                {selectedCampaign.budget && (
                  <div style={styles.infoRow}>
                    <span style={styles.infoLabel}>Budget:</span>
                    <span style={styles.infoValue}>{formatNumber(selectedCampaign.budget).toLocaleString('en-IN')}</span>
                  </div>
                )}
                {selectedCampaign.duration_days && (
                  <div style={styles.infoRow}>
                    <span style={styles.infoLabel}>Duration:</span>
                    <span style={styles.infoValue}>{selectedCampaign.duration_days} days</span>
                  </div>
                )}
              </div>

              <div style={styles.notesSection}>
                <label style={styles.notesLabel}>
                  Admin Notes {selectedCampaign.status === 'pending' && '(Required for rejection)'}
                </label>
                <textarea
                  style={styles.notesTextarea}
                  value={adminNotes}
                  onChange={(e) => setAdminNotes(e.target.value)}
                  placeholder="Add notes about this campaign..."
                  rows={4}
                  disabled={actionLoading}
                />
              </div>
            </div>

            <div style={styles.modalFooter}>
              <button
                style={{
                  ...styles.rejectButton,
                  ...(actionLoading ? { opacity: 0.6, cursor: 'not-allowed' } : {})
                }}
                onClick={handleReject}
                disabled={actionLoading}
              >
                {actionLoading ? 'Processing...' : 'Reject'}
              </button>
              <button
                style={{
                  ...styles.acceptButton,
                  ...(actionLoading ? { opacity: 0.6, cursor: 'not-allowed' } : {})
                }}
                onClick={handleAccept}
                disabled={actionLoading}
              >
                {actionLoading ? 'Processing...' : 'Accept'}
              </button>
            </div>
          </div>
        </div>
      )}

      {profileModalOpen && selectedAdvertiser && (
        <div style={styles.modalOverlay} onClick={closeProfileModal}>
          <div style={styles.modalContent} onClick={(e) => e.stopPropagation()}>
            <div style={styles.modalHeader}>
              <h3 style={styles.modalTitle}>Advertiser Profile</h3>
              <button style={styles.modalCloseButton} onClick={closeProfileModal}>
                
              </button>
            </div>

            <div style={styles.modalBody}>
              <div style={styles.profileHeaderSection}>
                <div style={styles.profileLogoWrapper}>
                  {selectedAdvertiser.brand_logo ? (
                    <img
                      src={selectedAdvertiser.brand_logo}
                      alt={selectedAdvertiser.brand_name || 'Brand logo'}
                      style={styles.profileLogo}
                    />
                  ) : (
                    <div style={styles.profileLogoFallback}>
                      {(selectedAdvertiser.brand_name || selectedAdvertiser.name || 'AD')[0]}
                    </div>
                  )}
                </div>
                <div style={styles.profileHeaderInfo}>
                  <h4 style={styles.profileTitle}>{selectedAdvertiser.brand_name || selectedAdvertiser.name || 'Unnamed Advertiser'}</h4>
                  <p style={styles.profileSubtitle}>{selectedAdvertiser.industry || 'General Advertiser'}</p>
                  {(() => {
                    const profileStatus = getAdvertiserStatus(selectedAdvertiser);
                    const profileBadgeColor =
                      profileStatus === 'approved'
                        ? '#34d399'
                        : profileStatus === 'pending'
                        ? '#fbbf24'
                        : profileStatus === 'rejected'
                        ? '#f87171'
                        : '#d1d5db';
                    const profileBadgeLabel =
                      profileStatus && profileStatus.length
                        ? `${profileStatus.charAt(0).toUpperCase()}${profileStatus.slice(1)}`
                        : selectedAdvertiser.status || 'Unknown';
                    return renderBadge(profileBadgeLabel, profileBadgeColor);
                  })()}
                </div>
              </div>

              <div style={styles.profileInfoSection}>
                <div style={styles.infoRow}>
                  <span style={styles.infoLabel}>Email:</span>
                  <span style={styles.infoValue}>{selectedAdvertiser.email || 'Not provided'}</span>
                </div>
                <div style={styles.infoRow}>
                  <span style={styles.infoLabel}>Phone:</span>
                  <span style={styles.infoValue}>{selectedAdvertiser.phone_number || 'Not provided'}</span>
                </div>
                <div style={styles.infoRow}>
                  <span style={styles.infoLabel}>Total Ads:</span>
                  <span style={styles.infoValue}>{selectedAdvertiser.total_ads ?? 0}</span>
                </div>
                <div style={styles.infoRow}>
                  <span style={styles.infoLabel}>Total Spent:</span>
                  <span style={styles.infoValue}>{formatNumber(selectedAdvertiser.total_spent).toLocaleString('en-IN')}</span>
                </div>
                {selectedAdvertiser.created_at && (
                  <div style={styles.infoRow}>
                    <span style={styles.infoLabel}>Created:</span>
                    <span style={styles.infoValue}>
                      {new Date(selectedAdvertiser.created_at).toLocaleString()}
                    </span>
                  </div>
                )}
                {selectedAdvertiser.approved_at && (
                  <div style={styles.infoRow}>
                    <span style={styles.infoLabel}>Approved:</span>
                    <span style={styles.infoValue}>
                      {new Date(selectedAdvertiser.approved_at).toLocaleString()}
                    </span>
                  </div>
                )}
                {selectedAdvertiser.business_name && (
                  <div style={styles.infoRow}>
                    <span style={styles.infoLabel}>Business Name:</span>
                    <span style={styles.infoValue}>{selectedAdvertiser.business_name}</span>
                  </div>
                )}
                {selectedAdvertiser.address && (
                  <div style={styles.infoRow}>
                    <span style={styles.infoLabel}>Address:</span>
                    <span style={styles.infoValue}>{selectedAdvertiser.address}</span>
                  </div>
                )}
              </div>

              {selectedAdvertiser.id && (
                <div style={styles.campaignsListSection}>
                  <h4 style={styles.sectionTitle}>Campaigns</h4>
                  <div style={styles.campaignsList}>
                    {ads.filter(ad => {
                      const adAdvertiserId = String(ad.advertiser_id || ad.advertiserId || '');
                      const adAdvertiserName = normalizeString(ad.advertiser || ad.advertiser_name);
                      const normalizedBrand = normalizeString(selectedAdvertiser.brand_name || selectedAdvertiser.name);
                      return adAdvertiserId === String(selectedAdvertiser.id) || normalizedBrand === adAdvertiserName;
                    }).map(ad => (
                      <div key={ad.id} style={styles.campaignListItem}>
                        <div>
                          <p style={styles.campaignName}>{ad.name || 'Untitled Campaign'}</p>
                          <p style={styles.campaignMeta}>
                            {renderBadge(ad.status || 'Unknown',
                              normalizeString(ad.status) === 'approved' ? '#34d399' :
                              normalizeString(ad.status) === 'pending' ? '#fbbf24' :
                              normalizeString(ad.status) === 'rejected' ? '#f87171' : '#d1d5db'
                            )}
                            <span style={{ marginLeft: '12px' }}>
                              Created: {new Date(ad.created_at || ad.createdAt || Date.now()).toLocaleDateString()}
                            </span>
                          </p>
                        </div>
                        <div style={styles.campaignMetrics}>
                          <span>Likes: {formatNumber(ad.likes)}</span>
                          <span>Shares: {formatNumber(ad.shares)}</span>
                          <span>Earnings: {formatNumber(ad.earnings).toLocaleString('en-IN')}</span>
                        </div>
                      </div>
                    ))}
                    {ads.filter(ad => {
                      const adAdvertiserId = String(ad.advertiser_id || ad.advertiserId || '');
                      const adAdvertiserName = normalizeString(ad.advertiser || ad.advertiser_name);
                      const normalizedBrand = normalizeString(selectedAdvertiser.brand_name || selectedAdvertiser.name);
                      return adAdvertiserId === String(selectedAdvertiser.id) || normalizedBrand === adAdvertiserName;
                    }).length === 0 && (
                      <p style={styles.noCampaigns}>No campaigns found for this advertiser</p>
                    )}
                  </div>
                </div>
              )}
            </div>

            <div style={styles.modalFooter}>
              <button
                style={styles.secondaryButton}
                onClick={closeProfileModal}
              >
                Close
              </button>
            </div>
          </div>
        </div>
      )}
    </section>
    </>
  );
};

