import React, { useState, useRef, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { apiBaseUrl } from '../config';

const AdvertiserLogin = () => {
  const VERIFIED_PHONE_KEY = 'advertiserVerifiedPhone';
  const PENDING_PHONE_KEY = 'advertiserPendingPhone';
  const navigate = useNavigate();
  const [phone, setPhone] = useState('');
  const [otpDigits, setOtpDigits] = useState(Array(6).fill(''));
  const [step, setStep] = useState('phone'); // 'phone' or 'otp'
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [advertiserExists, setAdvertiserExists] = useState(false);
  const [advertiserData, setAdvertiserData] = useState(null);

  const inputRefs = useRef([]);

  useEffect(() => {
    try {
      const storedPhone =
        localStorage.getItem(PENDING_PHONE_KEY) ||
        localStorage.getItem(VERIFIED_PHONE_KEY);

      if (storedPhone) {
        const digits = storedPhone.replace(/[^\d]/g, '').slice(-10);
        if (digits) {
          setPhone(digits);
        }
      }
    } catch (storageError) {
      console.warn('Unable to access stored advertiser phone:', storageError);
    }
  }, []);

  useEffect(() => {
    if (step === 'otp') {
      setOtpDigits(Array(6).fill(''));
      requestAnimationFrame(() => {
        inputRefs.current[0]?.focus();
      });
    }
  }, [step]);

  const handleSendOTP = async (e) => {
    e.preventDefault();
    if (!phone || phone.length < 10) {
      setError('Please enter a valid phone number');
      return;
    }

    // Format phone number with +91 prefix if not already present
    const formattedPhone = phone.startsWith('+91') ? phone : `+91${phone}`;

    setLoading(true);
    setError('');

    try {
      const response = await fetch(`${apiBaseUrl}/api/advertiser/send-otp`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ phone: formattedPhone }),
      });

      const data = await response.json();

      if (response.ok) {
        if (data.otp) {
          console.log(' DEVELOPMENT MODE: Your Advertiser OTP is:', data.otp);
          console.log(' Phone:', formattedPhone);
          console.log(' Use this OTP to verify your advertiser account');
        }
        try {
          localStorage.setItem(PENDING_PHONE_KEY, formattedPhone);
        } catch (storageError) {
          console.warn('Unable to store pending advertiser phone:', storageError);
        }
        setStep('otp');
        setError('');
      } else {
        setError(data.error || 'Failed to send OTP');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const handleVerifyOTP = async (e) => {
    e.preventDefault();
    const otpValue = otpDigits.join('');

    if (otpValue.length !== 6) {
      setError('Please enter a valid 6-digit OTP');
      return;
    }

    setLoading(true);
    setError('');

    const formattedPhone = phone.startsWith('+91') ? phone : `+91${phone}`;

    try {
      const response = await fetch(`${apiBaseUrl}/api/advertiser/verify-otp`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ phone: formattedPhone, otp_code: otpValue }),
      });

      const data = await response.json();

      if (response.ok) {
        try {
          localStorage.setItem(VERIFIED_PHONE_KEY, formattedPhone);
          localStorage.removeItem(PENDING_PHONE_KEY);
        } catch (storageError) {
          console.warn('Unable to store verified advertiser phone:', storageError);
        }

        if (data.advertiser_exists) {
          const advertiserInfo = data.advertiser;
          setAdvertiserData(advertiserInfo);
          setAdvertiserExists(true);
          navigate('/advertiser/dashboard', {
            state: { advertiserId: advertiserInfo.id }
          });
        } else {
          navigate('/advertiser/register', {
            state: { phone: formattedPhone }
          });
        }
      } else {
        setError(data.error || 'Invalid OTP');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const handleBackToPhone = () => {
    setStep('phone');
    setOtpDigits(Array(6).fill(''));
    setError('');
    try {
      localStorage.removeItem(PENDING_PHONE_KEY);
      localStorage.removeItem(VERIFIED_PHONE_KEY);
    } catch (storageError) {
      console.warn('Unable to clear advertiser phone cache:', storageError);
    }
  };

  const handleOtpChange = (index, value) => {
    const sanitized = value.replace(/\D/g, '');

    setOtpDigits(prev => {
      const next = [...prev];

      if (!sanitized) {
        next[index] = '';
        return next;
      }

      const char = sanitized[sanitized.length - 1];
      next[index] = char;

      if (index < next.length - 1) {
        inputRefs.current[index + 1]?.focus();
      }

      return next;
    });
  };

  const handleOtpKeyDown = (index, event) => {
    if (event.key === 'Backspace') {
      if (otpDigits[index]) {
        setOtpDigits(prev => {
          const next = [...prev];
          next[index] = '';
          return next;
        });
      } else if (index > 0) {
        inputRefs.current[index - 1]?.focus();
      }
    }

    if (event.key === 'ArrowLeft' && index > 0) {
      event.preventDefault();
      inputRefs.current[index - 1]?.focus();
    }

    if (event.key === 'ArrowRight' && index < otpDigits.length - 1) {
      event.preventDefault();
      inputRefs.current[index + 1]?.focus();
    }
  };

  const handleOtpPaste = (event) => {
    event.preventDefault();
    const pasteData = event.clipboardData.getData('text').replace(/\D/g, '');

    if (!pasteData) return;

    const chars = pasteData.slice(0, otpDigits.length).split('');
    setOtpDigits(prev => {
      const next = [...prev];
      chars.forEach((char, idx) => {
        next[idx] = char;
      });
      return next;
    });

    const targetIndex = Math.min(chars.length, otpDigits.length - 1);
    inputRefs.current[targetIndex]?.focus();
  };

  return (
    <div className="advertiser-login">
      <div className="advertiser-login-container">
        <div className="advertiser-login-header">
          <h1> Advertiser Login</h1>
          <p>Access your advertising dashboard and manage your campaigns</p>
        </div>

        {step === 'phone' ? (
          <form onSubmit={handleSendOTP} className="advertiser-login-form">
            <div className="form-group">
              <label htmlFor="phone">Phone Number</label>
              <div className="mobile-input-container">
                <span className="country-code">+91</span>
                <input
                  type="tel"
                  id="phone"
                  value={phone}
                  onChange={(e) => setPhone(e.target.value)}
                  placeholder="Enter your mobile number"
                  maxLength="10"
                  required
                />
              </div>
              <p className="helper-text">We'll send you a verification code</p>
            </div>

            {error && <div className="error-message">{error}</div>}

            <button
              type="submit"
              className="advertiser-login-btn"
              disabled={loading}
            >
              {loading ? 'Sending OTP...' : 'Send OTP'}
            </button>

            <div className="advertiser-login-footer">
              <p>
                Don't have an advertiser account?{' '}
                <span className="link" onClick={() => navigate('/advertiser/register')}>
                  Register here
                </span>
              </p>
              <p>
                <span className="link" onClick={() => navigate('/login')}>
                   Back to User Login
                </span>
              </p>
            </div>
          </form>
        ) : (
          <form onSubmit={handleVerifyOTP} className="advertiser-login-form">
            <div className="form-group">
              <label htmlFor="otp">Enter OTP</label>
              <div className="otp-input-group">
                {otpDigits.map((digit, index) => (
                  <input
                    key={index}
                    type="text"
                    inputMode="numeric"
                    pattern="[0-9]*"
                    maxLength={1}
                    className="otp-input-box"
                    value={digit}
                    onChange={(e) => handleOtpChange(index, e.target.value)}
                    onKeyDown={(e) => handleOtpKeyDown(index, e)}
                    onPaste={handleOtpPaste}
                    ref={el => (inputRefs.current[index] = el)}
                    aria-label={`OTP digit ${index + 1}`}
                    required
                  />
                ))}
              </div>
              <p className="otp-info">
                OTP sent to {phone.startsWith('+91') ? phone : `+91${phone}`}
              </p>
            </div>

            {error && <div className="error-message">{error}</div>}

            <button
              type="submit"
              className="advertiser-login-btn"
              disabled={loading}
            >
              {loading ? 'Verifying...' : 'Verify OTP'}
            </button>

            <div className="advertiser-login-footer">
              <p>
                <span className="link" onClick={handleBackToPhone}>
                   Change Phone Number
                </span>
              </p>
              <p>
                <span className="link" onClick={() => setStep('phone')}>
                  Resend OTP
                </span>
              </p>
            </div>
          </form>
        )}
      </div>
    </div>
  );
};

export default AdvertiserLogin;
